      <!--<h1>Original und Kopie des Rappoltsteiner Parzifal<br/> Handschriftliche berlieferung und Textgenese im 14. Jahrhundert</h1>-->
      <p class="MsoNormal">Der Rappoltsteiner Parzifal ist in zwei Handschriften berliefert: Karlsruhe, Badische Landesbibliothek,
        <a href="https://digital.blb-karlsruhe.de/blbhs/Handschriften/content/titleinfo/101664" target="_blank">Donaueschingen 97</a> (Hs. V: entstanden im Elsass, Raum Straburg/Colmar, oder in Basel, vollendet 1336), und Roma, Biblioteca Casanatense, <router-link to="/faksimile/VV/001r">Ms. 1409</router-link> (Hs. V': angefertigt in einer Epenwerkstatt im Raum Franken, 2. Viertel des 14. Jhs.). Die nach
        dem Entstehungsort benannte Textsammlung Rappoltsteiner Parzifal enthlt in Hs. V Wolframs von Eschenbach Parzival (aus der Zeit um 1200/10)<a href="#_ftn1" id="_ftnref1"><sup>[1]</sup></a> sowie darin  zwischen den Bchern XIV und XV  eingelagert den Nuwen Parzifal,<a href="#_ftn2" id="_ftnref2"><sup>[2]</sup></a> eine Kompilation  aus Conte du Graal-Fortsetzungen (zu Chrtiens de Troyes um 1190 unvollendet hinterlassenem Perceval-Roman) in deutscher bertragung,<a href="#_ftn3" id="_ftnref3"><sup>[3]</sup>
        </a> mit einigen weiteren krzeren Texten. Die Hs. V' hingegen berliefert nur diesen Nuwen Parzifal genannten Teil, der dort wie in Hs. V durch die abschlieenden beiden Bcher XV und XVI
        von Wolframs Parzival ergnzt wird. Sie kann, wie im Folgenden noch nher zu erlutern sein wird,
        als direkte Abschrift der Hs. V gelten. Insbesondere der in Hs. V vorliegende Text wird in der Forschung in mehrfacher
        Hinsicht als Glcksfall beurteilt. Immer wieder wird hingewiesen auf die ausfhrlichen Angaben zur Genese der Handschrift im Epilog des
        Rappoltsteiner Parzifal sowie auf die Tatsache, dass es sich bei Hs. V um eine Originalhandschrift handelt, also um jenes Exemplar, das
        der Auftraggeber in Bestellung gegeben hat. Dabei bleibt allerdings zu bercksichtigen, dass der in der Forschung gebrauchte Begriff des ,Originals stets die arbitrre Setzung eines ,Anfangs impliziert.<a id="_Ref348945462"/><a href="#_ftn4" id="_ftnref4"><sup>[4]</sup></a></p>
      <p class="MsoNormal">Die ungewhnliche Flle an Informationen zur Entstehung des Rappoltsteiner Parzifal, die der Epilog liefert, hat die
        Forschung seit der (Wieder-)Entdeckung des Werkes allerdings dazu verleitet, das Fehlen historisch bezeugter Fakten zum Leben des
        Auftraggebers sowie zu den am Herstellungsprozess der Handschrift beteiligten Personen auszublenden und mgliche Fiktionen als gesicherte
        Tatsachen darzustellen.<a href="#_ftn5" id="_ftnref5"><sup>[5]</sup></a> Gleichzeitig wurden die beiden Entstehungsvorgnge ,Genese des Werks
        (Rappoltsteiner Parzifal) und ,Genese der Handschrift meist nicht getrennt, sondern als ein einziger Vorgang betrachtet. Dies fhrte zu
        einer Vernachlssigung oder Ausblendung mglicher, heute verlorener Vor- und Zwischenstufen, die vor oder zwischen Text und Codex liegen,
        sowie zu einer vereinfachten Beschreibung der Entstehung der Handschrift. Die im durchkomponierten Epilog uerst romanhaften, fingierten
        Entstehungszusammenhnge haben auerdem zum Weiterdenken und zur Ausschmckung des Geschilderten verleitet, wie dies bereits bei der
        Darstellung <span style="font-variant:small-caps">Josephs Victor von Scheffel</span> von 1855 der Fall ist:<a href="#_ftn6" id="_ftnref6"><sup>[6]</sup></a></p>
      <blockquote class="Blockquote">Des edlen Herrn Ulrich von Rappoltstein Geschlecht war Trger eines Lehens ber die fahrenden Leute im Elsa,
        dessen Grenze bis an den Hagenauer Forst lief. Dies und ritterliche Freude an Minne und Milde mag den reichen Freiherrn bewogen haben, sich
        als Beschtzer der Dichtkunst das Werk Manessiers verdeutschen zu lassen. Im Jahr 1331 rckte zu diesem Behuf mit Manessiers franzsischem
        Buche nachfolgend verzeichnete, aus fnf, sage fnf Personen bestehende Gesellschaft bei ihm ein: 1. zwei Dichter, Claus Wisse und Philipp
        Kolin, Goldschmied von Straburg; 2. ein Dolmetsch, Sampson Pine, ein Jude; 3. zwei Schreiber, Henselin der junge und der von Ohnheim, ein
        alter. [] Dieses fahrende Volk, dessen Verpflegung manches Stckfa elsssischen Weines verschlungen haben mag, beeilte sich nicht
        allzusehr und berreichte erst im Jahre 1336 die vollendete, mit manchem Korrekturstreiflein berklebte, aber als stattlicher Foliant
        geschriebene Arbeit, von der sie bemerkten: und alles daz hie nach geschriben stat, daz ist ouch Parzeval ihrem Schirmherrn, dabei ihn
        launig ber die Kosten trstend, die sie selber auf 200 Pfund anschlugen.
      </blockquote>
      <p class="MsoNormal">In den folgenden Ausfhrungen werden die in der Forschung zum Rappoltsteiner Parzifal oftmals vermischten Ebenen, die
        <span style="letter-spacing:1.0pt">materielle</span> der Handschrift und die <span style="letter-spacing:1.0pt">inhaltliche</span> des Textes,
        jeweils getrennt betrachtet. Zunchst wird mit ausfhrlichen Handschriftenbeschreibungen und Inhaltsangaben der beiden bekannten Textzeugen des
        Rappoltsteiner Parzifal die materielle, historisch belegte Grundlage bereitgestellt (in <a href="#_Toc389815970">Kap. 1.1</a>
          fr Hs. V und <a href="#_Toc389815998">2.1</a> fr Hs. V'). Anschlieend werden die erhobenen Daten ausgewertet und in einem greren Kontext
            verortet. Dies geschieht im Hinblick auf die Darstellung
            der Genese des Textes (in <a href="#_Toc389815973">Kap. 1.2</a>), seiner Verschriftlichung (in <a href="#_Toc389815982">Kap. 1.3</a> fr Hs. V und
              <a href="#_Toc389816001">2.2</a> fr Hs. V') und bezglich der Geschichte nach der
                Erstanfertigung (in <a href="#_Ref389817114">Kap. 1.4</a>) sowie der Besitzgeschichte der Handschriften (in Kap.
                  <a href="#_Toc389815993">1.5</a> fr Hs. V und <a href="#_Toc389816005">2.3</a> fr Hs. V').</p>
      <h3><a id="_Toc389815969"/>1 Original: Handschrift V  Karlsruhe, Badische Landesbibliothek, Donaueschingen 97</h3>
      <p class="MsoNormal">Als Indiz dafr, dass es sich bei Hs. V um die <span style="letter-spacing:1.0pt">Originalhandschrift</span>
        handelt, fhrt der Herausgeber <span
        style="font-variant:small-caps">Karl Schorbach</span> die Tatsache an, dass
        der Codex in seiner besonderen, sorgfltig eingerichteten Textgestalt unter
        den Augen der Dichter fr Ulrich von Rappoltstein hergestellt wurde und
        noch aus dem 16. Jahrhundert Eintrge enthlt, die auf den Familienbesitz verweisen.<a href="#_ftn7" id="_ftnref7"><sup>[7]</sup></a> Nicht bekannt ist freilich, wie viele
        Vorstufen bzw. Entwrfe der vorliegenden Fassung vorausgegangen sind. Eine bemerkte und korrigierte Augenabirrung auf Bl. <router-link
        to="/faksimile/V/300v">300v</router-link>b, 5. Zeile von unten (NP 36200/36201; Sch. 839.39/40), weist darauf hin, dass zumindest Teile von Hs.
        V von einer Vorlage abgeschrieben worden sind und der Codex Donaueschingen 97 eine Reinschrift darstellt.<a href="#_ftn8"
        id="_ftnref8"><sup>[8]</sup></a> Angesichts dieser Umstnde soll der Begriff des ,Originals bzw. der ,Originalhandschrift im Folgenden pragmatisch fr den Sachverhalt verwendet werden, dass der Codex mit der darin berlieferten, aus Wolframs Parzival und den Conte du Graal-Fortsetzungen bestehenden Textgestalt von dem Mzen Ulrich von Rappoltstein in Auftrag gegeben worden und seinerseits zum Gegenstand einer in dem erwhnten Casanatense-Manuskript dokumentierten Abschrift geworden ist.</p>
      <p class="MsoNormal">Die Beschreibung des Codex und dessen Inhalt ergeben folgendes Bild:<a href="#_ftn9" id="_ftnref9"><sup>[9]</sup></a></p>
      <h4><a id="_Toc389815970"/>1.1 Beschreibung der Handschrift V</h4>
