        + I<sup><router-link to="/faksimile/V/320r">320</router-link>/<router-link to="/faksimile/V/321r">Spiegelbl.</router-link></sup>
          (einige Reklamanten noch erkennbar: Bll. <router-link to="/faksimile/V/060v">60v</router-link>,
          <router-link to="/faksimile/V/072v">72v</router-link>,
          <router-link to="/faksimile/V/084v">84v</router-link>,
          <router-link to="/faksimile/V/090v">90v</router-link>,
          <router-link to="/faksimile/V/096v">96v</router-link>,
          <router-link to="/faksimile/V/108v">108v</router-link>,
          <router-link to="/faksimile/V/139v">139v</router-link>,
          <router-link to="/faksimile/V/149v">149v</router-link>,
          <router-link to="/faksimile/V/161v">161v</router-link>,
          <router-link to="/faksimile/V/273v">273v</router-link>). <b>Schriftraum</b>:
          3333,5 x 1719,5 cm, zweispaltig (Spaltenbreite: 8,69,0 cm), Verse abgesetzt, Versalien in eigener Spalte durch
          unterschiedlich ausgeprgte Streicheisenlinien begrenzt (Vertikale teilweise mit Tinte). Endpunkte (Einritzungen) fr die Zeilen noch sichtbar,
          4855 (meist 49 oder 50) Zeilen pro Spalte. <b>Schrift</b>: Textura, fnf Hnde
          (siehe <a href="#_Toc389815983">Kap. 1.3.1</a>).<a href="#_ftn11" id="_ftnref11"><sup>[11]</sup></a><b> Ausstattung</b>: 127 berschriften
          und weitere Notate in roter Tinte (im Folgenden mit &lt;&gt; markiert), Namen rot gestrichelt. Fleuronne-Initialen von zwei verschiedenen Malerhnden in unterschiedlicher Qualitt mit roter und blauer, teilweise auch grner (z.B. Bll. <router-link to="/faksimile/V/017v">17v</router-link>b und <router-link to="/faksimile/V/022v">22v</router-link>a) Farbe (siehe
          <a href="#_Toc389815986">Kap. 1.3.2.1</a>), rote, blaue und auf Bl.
          <router-link to="/faksimile/V/288v">288v</router-link>a grne zweizeilige Lombarden
          mit Verzierung (siehe <a href="#_Toc389815987">Kap. 1.3.2.2</a>), Reprsentanten oft noch gut erkennbar (z.B. auf den Bll. <router-link to="/faksimile/V/028r">28r</router-link>a, <router-link to="/faksimile/V/136v">136v</router-link>a, <router-link to="/faksimile/V/137r">137r</router-link>a, <router-link to="/faksimile/V/137v">137v</router-link>a, <router-link to="/faksimile/V/138r">138r</router-link>b und <router-link to="/faksimile/V/139r">139r</router-link>a. Ungerade Versanfnge rubriziert. <b>Korrekturen/Nachtrge</b>:
          Praktisch keine Korrekturen (siehe <a href="#_Toc389815984">Kap. 1.3.1.1</a>). Zu den Nachtrgen siehe
          <a href="#_Toc389815992">Kap. 1.4.3</a> und <a href="#_Toc389815994">1.5.1</a>. Apokopierte Formen scheinen nachtrglich ergnzt worden zu sein
          (z.B. Bll. <router-link to="/faksimile/V/154r">154r</router-link>a,
          <router-link to="/faksimile/V/155r">155r</router-link>b,
          <router-link to="/faksimile/V/159v">159v</router-link>b und
          <router-link to="/faksimile/V/161v">161v</router-link>a).
          Mit Tinte korrespondierende Zeichen, die darauf hinweisen, dass die Bltter
          <router-link to="/faksimile/V/176r">176</router-link> und
          <router-link to="/faksimile/V/177r">177</router-link>
          falsch eingebunden wurden: Bl.
          <router-link to="/faksimile/V/175v">175v</router-link>b unter
          der Spalte, und Bl.
          <router-link to="/faksimile/V/178r">178r</router-link>a, nach
          erster Zeile (x in Kreis); Bl.
          <router-link to="/faksimile/V/176r">176r</router-link>a, nach
          erster Zeile, und Bl.
          <router-link to="/faksimile/V/179v">179v</router-link>a,
          unterer Rand (Hantel/Brille), sowie Bl.
          <router-link to="/faksimile/V/177v">177v</router-link>b,
          unter der Spalte, und Bl.
        <router-link to="/faksimile/V/180r">180r</router-link>a, erste Zeile (x). Bleistiftanmerkungen auf dem
        <router-link to="/faksimile/V/vs001v">Vorderspiegel</router-link>
          (Auflsung Abkrzungen der Devisen, siehe <a href="#_Toc389815994">Kap. 1.5.1</a>) sowie auf Bl.
          <router-link to="/faksimile/V/115r">115r</router-link>b (<i>Lachmann
            S. 345.</i>). <b>Zustand</b>: Abgegriffen, fleckig, Einband abgenutzt, mit
          Lchern durch Wurmfra.
      </p>
      <p class="MsoNormal"><b>Schreibsprache</b>:
          Niederalemannisch/elsssisch.<a href="#_ftn12" id="_ftnref12"><sup>[12]</sup></a></p>
