      <div style="clear:both"/>
      <h4><a id="_Toc389815973"/><a id="_Ref379010691"/>1.2 Genese des Textes  die Konzeption des <i>minnebuoch</i></h4>
      <p class="MsoNormal">Die Genese des Textes sowie die Entstehungsgeschichte der gesamten (?) Hs. V werden im 558
          Verse umfassenden Epilog (Ep 1558; Sch. 845.18858.26) in
          Form eines eigene(n), abgeschlossene(n) Stck(s) Kleinepik<a href="#_ftn16" id="_ftnref16"><sup>[16]</sup></a>
          ausfhrlich ,erzhlt. Der Epilog vollzieht auf diese Weise eine Faktisches wie Fiktionales vermischende literarische Inszenierung mit Personifikationen: So erwhlt die
          personifizierte <i>Minne</i>  veranlasst durch die personifizierte <i>Milte</i><a href="#_ftn17" id="_ftnref17"><sup>[17]</sup></a>
            einen Ulrich von Rappoltstein zu ihrem
          besonderen Diener und beauftragt ihn, ein <i>minnebuoch </i>(vgl. Ep 184;
          Sch. 849.44) schreiben zu lassen. Nicht gesagt wird, ob es sich bei diesem Buch
          um den gesamten Rappoltsteiner Parzifal oder lediglich um den Nuwen Parzifal
          handelt. Der in der Tradition der Minne- und Ehrenreden verfasste Epilog<a href="#_ftn18" id="_ftnref18"><sup>[18]</sup></a>
          besteht aus kunstvoll ineinander verschachtelten Gesprchssituationen zwischen
          <i>Minne</i>, <i>Milte</i>, dem Auftraggeber Ulrich von Rappoltstein
          und dessen Herzen sowie aus Schilderungen der Entstehung von Text und
          Texttrger durch einen Erzhler, der als Dichter-Ich auftritt. Der
          Epilog lsst sich inhaltlich wie folgt gliedern:<a href="#_ftn19" id="_ftnref19"> <sup>[19]</sup></a></p>
      <p class="metadata">berschrift mit Zuweisung des Epilogs an Philipp Colin (Ep 1-0; Sch. 845.1417), verbunden mit den bereits aus den Blattberechnungen (Hs. V, Bl. 115va, NP 0-1<sup>3f</sup>.) bekannten Titelbezeichnungen der Teile des Rappoltsteiner Parzifal, die in den folgenden Versen mittels Verfasserangaben przisiert werden:</p>
      <ul class="metadata">
          <li><i>der alte parzifal</i>: Wolframs von Eschenbach Parzival (vgl. Ep 124; Sch. 845.18846.16),</li>
          <li><i>der nvwe </i>[<i>parzifal</i>]: Philipp Colins <i>buch ... in tzch gerimet</i>  (vgl. Ep 2531; Sch. 846.1723).</li>
      </ul>
      <p class="metadata">Schilderung
            der im Auftraggeber wachsenden Minne sowie Betrachtung ber die Minne (Ep 3279; Sch. 846.24847.31).
      </p>
      <p class="metadata">Schilderung
            des Verhltnisses von <i>Minne </i>und<i> Milte</i> und ihrer Beteiligung an
            den Erzhlungen ber die Ritter der Tafelrunde (Ep 80116; Sch. 847.32848.22).
      </p>
      <p class="metadata" style="margin-left:35.45pt">Gesprch zwischen <i>Minne</i> und <i>Milte</i> ber
            den (noch nicht namentlich genannten) Auftraggeber mit der Ankndigung der <i>Minne</i>,
            dass sie diesem einen Minnebrief schreiben und persnlich berreichen werde (Ep 117150; Sch. 848.23849.10).
      </p>
      <p class="metadata" style="margin-left:35.45pt">Hinweis, dass der Brief an die Herzenswand geschrieben und
            von der <i>Minne</i> gelesen wurde. Erstmalige Nennung des Dichter-Ich und
            Ankndigung, dass der Diener der <i>Minne</i> (Ulrich von Rappoltstein) mit
            Name genannt werden wird (Ep 151167; Sch. 849.1127).
      </p>
      <p class="metadata" style="margin-left:35.45pt">Einleitung des
            Minnebriefs der <i>Minne</i> an Ulrich (Ep 168192; Sch. 849.28850.6).
      </p>
