      <p class="metadata">Anempfehlung Philipp Colins an Gott (Ep 524539; Sch. 857.18858.7).</p>
      <p class="metadata"><i>milte</i>-Appell des Dichters an das edle Herz (Ulrichs) (Ep 540558; Sch. 858.826).</p>
      <p class="MsoNormal">Der Auftraggeber Ulrich von
          Rappoltstein, dessen Wappenschild von der personifizierten <i>Milte</i>
          ausfhrlich beschrieben wird,<a href="#_ftn20" id="_ftnref20"><sup>[20]</sup></a> ist im Epilog zu einem
          wrdigen Nachfahren des sagenhaften Knig Artus stilisiert.<a href="#_ftn21" id="_ftnref21"><sup>[21]</sup></a>
          Die personifizierte <i>Minne</i> legt dar, dass Ulrich ein romanischsprachiges
          Buch besessen habe, in das Knig Artus selbst die Erzhlungen der Ritter der
          Tafelrunde schreiben lie und in dem er gerne las.<a href="#_ftn22" id="_ftnref22"><sup>[22]</sup></a>
          Ulrich wird dabei nicht nur als Artus Erbe und Blutsverwandter dargestellt,<a href="#_ftn23" id="_ftnref23"><sup>[23]</sup></a>
          sondern auch als vorbildliche(r) Minner[].<a href="#_ftn24" id="_ftnref24"><sup>[24]</sup></a></p>
      <p class="MsoNormal">                Im Epilog wird also mit
          fiktiven und allegorischen Mitteln ein Gnnerverhltnis inszeniert, das von
          faktischen Gegebenheiten geprgt zu sein scheint. Die Angaben zur Genese der
          Handschrift sind im Kontext der literarischen Stilisierung (Liebesbrief
          der <i>Minne</i>) jedoch schlssig und drften sich auf historische
          Sachverhalte beziehen, die im Folgenden nachgezeichnet werden sollen.
      </p>
      <h5>
                          <a id="_Toc389815974"/>
                          <a id="_Ref381896899"/>
                          <a id="_Ref381540316"/>
                          <a id="_Ref381538261"/>
                          <a id="_Ref381537778"/>
                          <a id="_Ref381537686"/>
                          <a id="_Ref381367850"/>1.2.1 Der Auftraggeber Ulrich von Rappoltstein
      </h5>
      <p class="MsoNormal">Gem dem Epilog wurde der
          Rappoltsteiner Parzifal von einem an mehreren Stellen namentlich genannten
          Ulrich, Freiherr von Rappoltstein (Ribeaupierre),<a href="#_ftn25" id="_ftnref25"><sup>[25]</sup></a>
          in Auftrag gegeben. Stammsitz und Zentrum der Herrschaft der edelfreien Familie
          von Rappoltstein war der auf das 8. Jahrhundert zurckgehende, nrdlich von
          Colmar gelegene Ort Rappoltsweiler (Ribeauvill) mit drei oberhalb davon
          erbauten Hhenburgen. Die ab Ende des 13. Jahrhunderts befestigte Kleinstadt
          Rappoltsweiler hatten die Herren von Rappoltstein als Lehen des Basler
          Bischofs erhalten. Zu Rappoltsweiler gehrten die folgenden separat verwalteten
          Stadtviertel, von denen jedes durch eine eigene Ringmauer abgegrenzt war: (1) der
          Siedlungskern (spter: <span style="letter-spacing:1.0pt">Altstadt</span>), (2)
          die oberhalb (westlich) gelegene <span style="letter-spacing:1.0pt">Neustadt</span>,
          (3) die unterhalb (stlich) gelegene <span style="letter-spacing:1.0pt">Unterstadt</span>
          (<i>Niderstat</i>; <i>Nidern statt</i>) sowie (4) die westlich anschlieende <span style="letter-spacing:1.0pt">Oberstadt</span> (Oberdorf). Die drei Burgen der
          Herren von Rappoltstein oberhalb der Stadt waren (1) die Stammburg <span style="letter-spacing:1.0pt">Gro-Rappoltstein</span> (Grand-Ribeaupierre) (<i>castrum
            inferius</i>, Niederburg, St. Ulrichsburg oder auch einfach Burg Rappoltstein),
          (2) die Burg <span style="letter-spacing:1.0pt">Hoh(en)-Rappoltstein</span>
          (Haut-Ribeaupierre) (<i>castrum superius</i>, <i>altum castellum</i>, <i>Altencastel</i>, <i>Altenkasten</i>) sowie (3) die Burg <span style="letter-spacing:1.0pt">Girsberg</span>
          (<i>der Stein</i>, Klein-Rappoltstein, Petit-Ribeaupierre).<a href="#_ftn26" id="_ftnref26"><sup>[26]</sup></a></p>
      <p class="MsoNormal">                Von den im
          Entstehungszeitraum der Handschrift bekannten Trgern des Namens Ulrich von
          Rappoltstein aus drei Generationen kommen folgende als Auftraggeber der
          Handschrift infrage:<a href="#_ftn27" id="_ftnref27"><sup>[27]</sup></a></p>
      <p class="MsoNormal">1. Der Edelmann <span style="letter-spacing:1.0pt">Ulrich,</span> unverheirateter Sohn des Ritters und
          Herren zu Hoh(en)-Rappoltstein Anselm ( 1311) und Bruder des 1335 bereits
          verstorbenen Johann, Herr zu Hoh(en)-Rappoltstein. 1310 ist Ulrich als Student
          in Bologna belegt<a href="#_ftn28" id="_ftnref28"><sup>[28]</sup></a> und noch 1335 als
          Domherr (Canonicus) in Straburg ttig.<a href="#_ftn29" id="_ftnref29"><sup>[29]</sup></a></p>
      <p class="MsoNormal">2. Ein weiterer <span style="letter-spacing:1.0pt">Ulrich</span>, Sohn des Heinrich III., war 1313
          noch minderjhrig und ist 1328 als Komtur der Dorlisheimer Johanniter bezeugt.<a href="#_ftn30" id="_ftnref30"><sup>[30]</sup></a></p>
      <p class="MsoNormal">3. Der Sohn Johanns, des Ritters
          und Herren zu Hohen-Rappoltstein und ab 1321 auch zu Hohenack ( 1362), <span style="letter-spacing:1.0pt">Ulrich IV</span>., der im Jahr 1337 Domherr in
          Basel und Kirchherr von Reichenweier (Riquewihr) war.<a href="#_ftn31" id="_ftnref31"><sup>[31]</sup></a>
          Wahrscheinlich als Domherr zurckgetreten, lebte er sptestens ab 1341 auf der
          der Burg Hoh(en)-Rappoltstein.<a href="#_ftn32" id="_ftnref32"><sup>[32]</sup></a> Im Jahr 1353 (Eingang
          der Mitgift) vermhlte er sich mit Herzelaude (Herzlanda, Loveline) ( um
          1362), Tochter des Grafen Gottfried (Gtz) von Frstenberg-Haslach und der
          Grfin Anna von Montfort (Heirat 1323, beide  1341). <span style="letter-spacing: 1.0pt">Ulrich IV.</span> verstarb im Jahr 1377 und wurde in der
          Zisterzienserabtei Pairis (nahe Kaysersberg im Elsass, siehe dazu Kap.
          <a href="#_Toc389815988">1.3.2.3</a>) begraben.<a href="#_ftn33" id="_ftnref33"><sup>[33]</sup></a></p>
      <p class="MsoNormal">Bislang konnte nicht eindeutig
          nachgewiesen werden, welcher Ulrich der Auftraggeber des Rappoltsteiner
          Parzifal war, favorisiert werden jedoch der Sohn des Anselm (1.) und der Sohn
          des Johann (3.).<a href="#_ftn34" id="_ftnref34"><sup>[34]</sup></a> Diese beiden wurden
          zusammen mit Johann, dem Bruder des letztgenannten Ulrich, 1341 von den kinderlosen
          Herren von Hoh(en)rappoltstein und Herren der Niederstadt, Johann und Anselm,
          den Shnen des Johann, Herrn von Hoh(en)rappoltstein, als Erben des Lehens
          eingesetzt, das sie nach dem baldigen Tod von Johann und Anselm auch bernahmen.<a href="#_ftn35" id="_ftnref35"><sup>[35]</sup></a></p>
      <h6>
                          <a id="_Toc389815975"/>
                          <a id="_Ref381368028"/>1.2.1.1 Abfassungszeit
      </h6>
      <p class="MsoNormal">Das Jahr der Vollendung wird in Hs. V innerhalb der Prosaberleitung auf
          Bl.
          <router-link to="/faksimile/V/115r">115r</router-link>b<router-link to="/faksimile/V/115v">v</router-link>a mit 1336 angegeben:<a href="#_ftn36" id="_ftnref36"><sup>[36]</sup></a></p>
      <blockquote class="Blockquote" style="text-indent:.1pt">
                          <span class="titel1">
                                <i>
                                        <span style="font-size:0.9rem">&lt;vn(de) alles daz hie nach ge|chriben
                  tat. das it och parzefal. Vn(de) it | von welche zv tzche braht. vn(de)
                  volle|tihtet. vn(de) zv ende braht. Dis gechach | do men zalte von gotz
                  gebrte. drize|hundert ior. Vn(de) drizig ior. in deme | ehten iore&gt; </span>
                                    </i>
                            </span>
                          <span class="titel1">
                                <span style="font-size:0.9rem">(NP 0-1<sup>2</sup>, B. 115va)</span>
                            </span>
      </blockquote>
      <p class="MsoNormal">Zu Recht hat <span style="font-variant:small-caps">Manfred Gnter Scholz</span> darauf
          hingewiesen, dass nicht zu entscheiden ist, ob sich die Jahresangabe zeitlich auf die Vollendung des Nuwen Parzifal, seiner Niederschrift oder
          vielleicht der der gesamten Hs. V bezieht.<a href="#_ftn37" id="_ftnref37"><sup>[37]</sup></a>
          Jedenfalls hat sich die Gesamtherstellung der Handschrift gem Angaben
          im Epilog und im Kolophon auf fnf Jahre (d.h. also wohl: auf die Jahre von 1331 bis 1336) erstreckt.<a href="#_ftn38" id="_ftnref38"><sup>[38]</sup></a></p>
      <h6>
                          <a id="_Toc389815976"/>1.2.1.2 Abfassungsort
      </h6>
      <p class="MsoNormal"><span style="font-variant:small-caps">Schorbach</span>
          vermutet, dass die Hs. V in der Burg Gro-Rappoltstein (St. Ulrichsburg)
          entstanden ist.<a href="#_ftn39" id="_ftnref39"><sup>[39]</sup></a> Whrend der
          Abfassungszeit des Rappoltsteiner Parzifal in den Dreiigerjahren des 14.
          Jahrhunderts war der Edelmann Johann von Rappoltstein ( 1362), Herr zu
          Gro-Rappoltstein und Herr in der Rappoltsweiler Neustadt und Oberstadt.<a href="#_ftn40" id="_ftnref40"><sup>[40]</sup></a>
          Er war der Ehemann der Elisabeth von Geroldseck,<a href="#_ftn41" id="_ftnref41"><sup>[41]</sup></a>
          der Bruder der Sophia, btissin von Andlau,<a href="#_ftn42" id="_ftnref42"><sup>[42]</sup></a> und der Vater Johanns
          und Ulrichs IV., der als Auftraggeber des Rappoltsteiner Parzifal infrage
          kommt (siehe <a href="#_Toc389815974">Kap. 1.2.1</a>). Ob Ulrich IV. zum Zeitpunkt der Fertigstellung von
          Hs. V noch auf der Burg Gro-Rappoltstein wohnhaft war oder bereits als Domherr
          in Basel weilte, ist nicht bekannt. Der zweite Ulrich, der als Auftraggeber
          diskutiert wird, nmlich der Sohn des Anselm, war whrend der Abfassung des
          Textes und der Herstellung der Handschrift wahrscheinlich noch Domherr in
          Straburg. Sowohl die Burg Gro-Rappoltstein als auch die Stdte Basel und
          Straburg knnen somit als Entstehungsort des Rappoltsteiner Parzifal und der
          Hs. V in Betracht gezogen werden.
      </p>
      <h5>
                          <a id="_Toc389815977"/>1.2.2 Konzepteure, Kompilatoren, Dichter und bersetzer
      </h5>
      <p class="MsoNormal">Der Epilog berichtet nicht nur
          vom Auftraggeber Ulrich von Rappoltstein. Es werden auch drei am Herstellungsprozess
          der Artusdichtung Rappoltsteiner Parzifal beteiligte Personen namentlich
          aufgefhrt:
      </p>
      <h6>
                          <a id="_Toc389815978"/>1.2.2.1 Philipp Colin
      </h6>
      <p class="MsoNormal">Bereits in einer vorangehenden Rubrik
          wird Philipp Colin (<span class="titel1"><i>pfilippes kolin</i></span>) als
          Verfasser des Epilogs genannt.<a href="#_ftn43" id="_ftnref43"><sup>[43]</sup></a> Der Epilog seinerseits
          stellt Colin als klugen
          (kundigen?) Goldschmied<a href="#_ftn44" id="_ftnref44"><sup>[44]</sup></a> aus oder wohnhaft in
          Straburg dar, der das vorliegende Buch
          fr seinen Herrn aus franzsischen in deutsche Reime gebracht habe.<a href="#_ftn45" id="_ftnref45"><sup>[45]</sup></a>
        Nach den anfnglichen Nennungen in der dritten Person tritt ab Vers Ep 163 (Sch. 849.23) ein wohl auf Philipp Colin verweisender
        Ich-Erzhler auf.<a href="#_ftn46" id="_ftnref46"><sup>[46]</sup></a> An spterer Stelle berechnet dieser fr die
          Herstellung des Rappoltsteiner Parzifal Kosten in der Hhe von 200 (vermutlich
          Straburger) Pfund Pfennigen (87,5 Mark Silber), den Gegenwert dessen, was
        <i>ein minner in kvrzer tunt | An eime orze vertichet</i> (eines Turnierpferds?, Ep 405411; Sch. 854.3743). Es wird im Epilog jedoch nicht gesagt, wer
          von den am Entstehungsprozess beteiligten Personen (siehe Kap. <a href="#_Toc389815979">1.2.2.2</a>,
          <a href="#_Toc389815980">1.2.2.3</a>, <a href="#_Toc389815983">1.3.1</a> und
          <a href="#_Toc389815985">1.3.2</a>) von dieser Summe profitieren konnte und ob darin ein
          bereits geleistetes Entgelt in Naturalien mitgezhlt wurde.<a href="#_ftn47" id="_ftnref47"><sup>[47]</sup></a></p>
      <p class="MsoNormal">Bei dem erwhnten
          Philipp Colin handelt es sich mglicherweise um den urkundlich 1307 und 1309
          bezeugten<a href="#_ftn48" id="_ftnref48"><sup>[48]</sup></a> Sohn des in Straburg
          wohnhaften Burkhard Kolin und Bruder des Klaus Kolin<a href="#_ftn49" id="_ftnref49"><sup>[49]</sup></a>
          sowie der Begine Anna. Dieser Philipp Colin hat zusammen mit seiner Gattin
          Katherine<a href="#_ftn50" id="_ftnref50"><sup>[50]</sup></a> am 5.9.1309 fr 21 Mark
          Straburger Silber seinen Anteil an der <span lang="DE-CH">Rheinsfahre
            (Rheinbergangsstelle) St. Johann in undis (Viertel Krutenau in Straburg) und
            Hundsfeld (Gemarkung Eckartsweier bei Kehl)</span> <span lang="DE-CH">an den Straburger Brger Diether Klbelin</span> <a href="#_ftn51" id="_ftnref51"><sup>[51]</sup></a>
          veruert, dem Philipps Schwester, die Begine Anna, bereits am 29.4.1309 ihren
          Anteil fr 20 Mark Silber verkauft hatte.<a href="#_ftn52" id="_ftnref52"><sup>[52]</sup></a></p>
      <h6><a id="_Toc389815979"/><a id="_Ref381896925"/><a id="_Ref381536118"/><a id="_Ref379125397"/>1.2.2.2 Klaus Wisse</h6>
      <p class="MsoNormal">Mitten im Epilog, im Anschluss an
        die oben zitierte Androhung einer ,Bannsetzung Ulrichs von Rappoltstein durch die Minne (Ep 338342; Sch. 853.1620) und nach dem erteilten Auftrag, das
          Werk abzufassen, wird gem dem Wortlaut von Hs. V ber die Entstehung des Rappoltsteiner
          Parzifal Folgendes berichtet:
      </p>
      <p class="verse">
                          <i>An die Ouentre</i>
      </p>
      <p class="verse">
                          <i>Vier vernent vn(de) vier hre</i>
      </p>
      <p class="verse">
                          <i>Het dirre herre enthalten mich</i>
      </p>
      <p class="verse">
                          <i>Obe dieme buche kotlich</i>
      </p>
      <p class="verse">
                          <i>Vnde darzv einen chribere</i>
      </p>
      <p class="verse">
                          <i>Vnde ein anderen tihtere</i>
      </p>
      <p class="verse">
                          <i>Der tihtete dien anevang</i>
      </p>
      <p class="verse">
                          <i>Men ol ez i(m)me gerne agen dang</i>
      </p>
      <p class="verse">
                          <i>Wan er it ein tihter clug</i>
      </p>
      <p class="verse">
                          <i>Vn(de) kan darzv guten gefug</i>
      </p>
      <p class="verse">
                          <i>Er it genant clawez wize</i>
      </p>
      <p class="verse">
                          <i>Jch wnche i(m)me daz er lize</i>
      </p>
      <p class="verse">
                          <i>Sine tage under were</i>
      </p>
      <p class="verse">
                          <i>Als ein cluger minnere</i>
      </p>
      <p class="verse">
                          <i>Der tihtete ein ior vor mir .e.</i>
      </p>
      <p class="verse">(Ep 365379; Sch. 853.43854.11)
      </p>
      <p class="MsoNormal">Demnach hat ein Jahr vor Colin
          der als Dichter bezeichnete Klaus Wisse den Anfang des Rappoltsteiner Parzifal
          erstellt. Ob sein Beitrag lediglich den Prologus, also die bertragung der altfranzsischen
          lucidation, umfasst hat, wie dies <span style="font-variant:small-caps">Schorbach</span>
          vermutet, lsst sich nicht entscheiden.<a href="#_ftn53" id="_ftnref53"><sup>[53]</sup></a> Eine Vermutung bleibt
          zudem <span style="font-variant:small-caps">Jschke</span>s Gleichsetzung mit
          einem <i>Clawes </i>(<i>Claves</i>)<i> Wisse</i>, der am Straburger ,Geschelle
          der Geschlechter Zorn und Mllenheim vom 20. Mai 1332 mit gezogenem Schwert teilgenommen
          hat und mglicherweise durch die Folgen von der Gemeinschaftsarbeit abgezogen
          worden ist.<a href="#_ftn54" id="_ftnref54"><sup>[54]</sup></a></p>
      <h6>
                          <a id="_Toc389815980"/>
                          <a id="_Ref381896941"/>
                          <a id="_Ref381536191"/>
                          <a id="_Ref379125415"/>1.2.2.3 Samson Pine
      </h6>
      <p class="MsoNormal"><a id="_Ref381536149"/>Neben
        Philipp Colin und Klaus Wisse wird im Epilog auch Samson Pine erwhnt,
        der die altfranzsischen Textanteile offenbar (mndlich?) bersetzt hat, so dass
        mithilfe dieser Untersttzung die deutschen Verse des Rappoltsteiner Parzifal
        angefertigt werden konnten. Da Samson Pine jdischer Herkunft war,
        zeichnet sich hier eine literarische Zusammenarbeit ber religise Grenzen hinweg ab.<a href="#_ftn55" id="_ftnref55"><sup>[55]</sup></a>
        Ambivalent bleiben allerdings die im Epilog zum Ausdruck gebrachten Dankesworte und Wnsche,
        welche in die Aussage mnden, dass der bersetzer mehr nicht zu ,begehren brauchte oder ,begehrte:
      </p>
      <p class="verse">
                          <i>Ein jude it ampon pine gena(n)t</i>
      </p>
      <p class="verse">
                          <i>Der het ine zit och wol bewant</i>
      </p>
      <p class="verse">
                          <i>An dirre ouenture</i>
      </p>
      <p class="verse">
                          <i>Er tet vnz die tre</i>
      </p>
      <p class="verse">
                          <i>Waz wir zv rimen hant bereit</i>
      </p>
      <p class="verse">
                          <i>Do het er vnz daz tchch geeit</i>
      </p>
      <p class="verse">
                          <i>Von den ouenturen allen gar</i>
      </p>
      <p class="verse">
                          <i>Jch wnche daz er wol geuar</i>
      </p>
      <p class="verse">
                          <i>Al ein iude noch inre .e.</i>
      </p>
      <p class="verse">
                          <i>Er enbegerte anders nvt me</i>
      </p>
      <p class="verse">(Ep 395404; Sch. 854.2736)
      </p>
      <p class="MsoNormal">Der Versabschnitt steht unmittelbar vor der oben erwhnten Kostenberechnung
        und knnte damit implizit auch mit Fragen der Entlohnung in Zusammenhang stehen.
        Als Angehriger des jdischen Glaubens war Samson Pine Leibeigener des Kaisers, in dessen Dienst die Juden
          standen und an den sie Abgaben zu leisten hatten. Bei Bedarf konnte der Kaiser
          die Juden gegen Entgelt verpfnden, was laut einer 1331 in Nrnberg
          ausgestellten Urkunde auch in der Stadt Rappoltsweiler geschah: So verpfndete
          am 15. Februar 1331 Kaiser Ludwig der Bayer fr 400 Mark Silber die Juden der
          oberen und niederen Stadt zu Rappoltsweiler dem damaligen Herrn in der
          Oberstadt, Johannes von Rappoltstein, Vater Ulrichs IV.<a href="#_ftn56" id="_ftnref56"><sup>[56]</sup></a>
          Falls auch Samson Pine der seit Beginn des 14. Jahrhunderts nachweisbaren jdischen Gemeinde
          in Rappoltsweiler<a href="#_ftn57" id="_ftnref57"><sup>[57]</sup></a> angehrt hat, knnte er als
          Leibeigener des Johannes von Rappoltstein zur Mitarbeit am Rappoltsteiner
          Parzifal verpflichtet worden sein.
      </p>
      <h5><a id="_Toc389815981"/>1.2.3 Benutzte Vorlagen</h5>
      <p class="MsoNormal">Als Quellen fr den
          Rappoltsteiner Parzifal dienten den Kompilatoren und Redaktoren
          unterschiedliche deutsche und franzsische Texte. Von den fr Hs. V benutzten
          handschriftlichen Vorlagen hat sich allerdings keine erhalten. Trotz dieses
          Verlusts knnen aus der Flle der noch bekannten berlieferungszeugen Handschriften
          ermittelt werden, die den im Rappoltsteiner Parzifal berlieferten Textfassungen
          nher stehen als andere.
      </p>
      <p class="MsoNormal">So folgt der Prologus einer Versfassung der altfranzsischen lucidation, die dem Text der einzig bekannten
          Handschrift, des Codex Mons, Bibliothque centrale, <a href="http://biblio.umons.ac.be/public/bv/Perceval-complet_004/" target="_blank">331/206 R2/C</a>,
          aus dem 13. Jahrhundert zwar nahesteht, sich an einigen Stellen jedoch mit der
          altfranzsischen Prosafassung im Pariser Druck von 1530 gegen diesen verhlt.<a href="#_ftn58" id="_ftnref58"><sup>[58]</sup></a>
          Fr die Verwendung von Textstellen aus Chrtiens de Troyes Conte du Graal,
          die in der Vorlage wahrscheinlich mit der lucidation gemeinsam berliefert
          wurden, kann keine der in <span style="font-variant:small-caps">Keith Busby</span>s
          Ausgabe<a href="#_ftn59" id="_ftnref59"><sup>[59]</sup></a> bercksichtigten
          Handschriften in eine direkte Verbindung zum Rappoltsteiner Parzifal gebracht
          werden.<a href="#_ftn60" id="_ftnref60"><sup>[60]</sup></a> Dasselbe gilt auch fr
          Stellen aus den Continuations zu Chrtiens Conte du Graal, die zwar der bekannten
          Mischredaktion folgen, jedoch gem den Lesarten keiner der erhaltenen Handschriften
          direkt zugeordnet werden knnen.
      </p>
      <p class="MsoNormal">                Den Parzival Wolframs von Eschenbach hingegen berliefert der Rappoltsteiner Parzifal nachweislich
          in einem mit einer *T- und mit einer *m-Fassung kontaminierten Text, in dem
          unterschiedliche Stufen der Be- und berarbeitung beobachtet werden knnen.<a href="#_ftn61" id="_ftnref61"><sup>[61]</sup></a><span style="font-variant:small-caps"> Robert Schller</span> fasst die komplizierte
          Genese des Parzival-Textes in Hs. V wie folgt zusammen:
      </p>
      <blockquote class="Blockquote">Der Text von Parzival V wurde zunchst nach einer
            Vorlage aus *T abgeschrieben. In einem zweiten Durchgang wurde eine Handschrift
            aus der Gruppe *m nachverglichen. Wo die Handschrift aus *m im Textbestand
            uber die *T-Vorlage hinausging, wurden die zustzlichen Verse zunchst am Rand
            notiert. Daruber hinaus wurden ganze Bltter (50, 51, 5557, 59, 6366, 69 und
            87) aus dem Codex geschnitten, nach der Vorlage von *m neu angefertigt und an
            der entsprechenden Stelle eingeklebt. Ab Buch XV wurde als erste Vorlage diese
            Handschrift aus *m verwendet. In einem dritten Durchgang wurde schlielich noch
            eine Vorlage aus *QR nachverglichen. Die am Rand notierten Verse von *m wurden
            radiert und in den Text integriert, aber nur dann, wenn diese Verse auch in *QR
            vorhanden waren.<a href="#_ftn62" id="_ftnref62"><sup>[62]</sup></a>
      </blockquote>
      <p class="MsoNormal">Die direkten Vorlagen von Hs. V
          aus den Fassungen *T und *m (fr die Korrekturen sowie fr den Grundbestand ab
          dem Parzival-Buch XV) haben sich nicht erhalten. Sie sind wahrscheinlich wie
          die noch bekannten, zwischen dem ersten Viertel des 13. und dem ersten Viertel
          des 14. Jahrhunderts geschriebenen *T-Textzeugen im alemannischen Raum
          entstanden.<a href="#_ftn63" id="_ftnref63"><sup>[63]</sup></a> Die erhaltenen *m-Textzeugen
          sind zwar jnger als Hs. V, knnen jedoch im mglichen Entstehungsraum des
          Rappoltsteiner Parzifal lokalisiert werden: Alle vier berlieferungszeugen stammen
          nachweislich aus Straburg. Es sind dies drei nicht voneinander abgeschriebene,
          aber alle im zweiten Viertel des 15. Jahrhunderts in der elsssischen
          Lauberwerkstatt gefertigte Handschriften sowie ein aus der Straburger Offizin des
          Johannes Mentelin stammender Druck von 1477.<a href="#_ftn64" id="_ftnref64"><sup>[64]</sup></a> Die letzte Handschrift,
          die bei der Herstellung von Hs. V in einem dritten Schritt zur Kontrolle und
          zum Vergleich beigezogen wurde, ist mit der Vorlage *QR verwandt. Die einzigen erhaltenen
          Handschriften dieser Gruppe (Hs. Q und die mit kolorierten Federzeichnungen
          ausgestattete Hs. R) sind im dritten Viertel des 15. Jahrhunderts in Ostfranken
          bzw. in Konstanz entstanden.<a href="#_ftn64" id="_ftnref64"><sup>[64]</sup></a></p>
      <p class="MsoNormal">                Isolierte
          Freidank-Verse wie derjenige, der in Hs. V auf dem Spruchband von Bl.
          <router-link to="/faksimile/V/115v">115v</router-link>a berliefert ist (FR 110,21f.), sind im Mittelalter keine Seltenheit. Aus dem
          16. Jahrhundert hat sich beispielsweise eine Balkenmalerei auf der Frontseite eines Gebudes in Fritzlar (Markt 28) erhalten, die den Vers
          gemeinsam mit den Versen FR 62,12f. wiedergibt.<a href="#_ftn66" id="_ftnref66"><sup>[66]</sup></a></p>
      <p class="MsoNormal">                Die sieben bekannten
          Minneliedstrophen im sogenannten Rappoltsteiner Florilegium auf demselben Bl.
          <router-link to="/faksimile/V/115v">115v</router-link> sind
          einzeln weit verbreitet, finden sich in dieser Zusammenstellung jedoch lediglich
          in Hs. V und Hs. V';<a href="#_ftn67" id="_ftnref67"><sup>[67]</sup></a> eine weitere auf Bl.
          <router-link to="/faksimile/V/320v">320v</router-link>a
          nachgetragene Strophe wiederum berliefert nur Hs. V.
      </p>
      <h4><a id="_Toc389815982"/><a id="_Ref379010745"/>1.3 Genese der Handschrift V
      </h4>
      <p class="MsoNormal">Nach der Darstellung der Genese
          der Texte im Rappoltsteiner Parzifal wird im Folgenden mittels
          palographischer Untersuchungen die Genese des berlieferungstrgers Hs. V
          nachgezeichnet. Bercksichtigt werden dabei neben dem Schreiberanteil (Kap. 1.3.1) und
          dem Korrekturprozess (Kap. 1.3.1.1) der fnf unterscheidbaren Schreiberhnde
          auch die von professioneller Hand ausgefhrte ornamentale Ausstattung der
          Handschrift (Kap. 1.3.2).
      </p>
      <h5><a id="_Toc389815983"/><a id="_Ref379014675"/>1.3.1 Die Schreiber</h5>
      <p class="MsoNormal"><a id="_Ref381536263"/>Auf Bl.
          <router-link to="/faksimile/V/320v">320v</router-link>a von
          Hs. V werden in einem mit roter Tinte geschriebenen Kolophon ein Schreiber <i>Henelin
            </i>sowie ein weiterer, aus <i>Onhein</i> stammender Mann genannt:<a href="#_ftn68" id="_ftnref68"><sup>[68]</sup></a></p>
      <p class="verse"><i>Diz ol nieman v(er)geen</i></p>
      <p class="verse"><i>Ob diem buche int fnf ior geeen</i></p>
      <p class="verse"><i>Ze tihtende vn(de) ze chriben</i></p>
      <p class="verse"><i>Hie ol ein ende b::ben</i></p>
      <p class="verse"><i>Henelin chrib(er) het och vil gechribe(n) h(er)an</i></p>
      <p class="verse"><i>Vn(de) wil noch nt ein ende han</i></p>
      <p class="verse"><i>Er gewan noch nie bart</i></p>
      <p class="verse"><i>Vn(de) it och den vinen vrowelin zart</i></p>
      <p class="verse"><i>Der von Onhein it ein reht(er) tore</i></p>
      <p class="verse"><i>Er trget die vrowen mit ime growe(n) hore</i></p>
      <p class="verse">(Ep 558-3<sup>110</sup>; Sch. XVI)</p>
      <p class="MsoNormal">Nicht eindeutig bestimmen lsst sich in diesem Eintrag, ob es sich beim Namen <i>Henselin</i> um eine Kurzform
          des Vornamens Johannes oder um einen Nachnamen handelt und ob mit <i>Schriber</i> eine Berufsbezeichnung oder ein
          Nachname gemeint ist.<a href="#_ftn69" id="_ftnref69"><sup>[69]</sup></a> Infrage kommen somit
          Vertreter folgender Namenskombinationen: <i>Henselin</i> (Johannes) <i>Schriber</i>
          (Nachname), ein Schreiber mit Vorname <i>Henselin</i> (Johannes)<a href="#_ftn70" id="_ftnref70"><sup>[70]</sup></a>
          sowie ein Schreiber mit Nachname <i>Henslin</i><a href="#_ftn71" id="_ftnref71"><sup>[71]</sup></a>.
          Zudem wird im Kolophon nicht gesagt, ob dieser Schreiber ein Stadtschreiber
          oder ein Berufsschreiber im Dienste des Auftraggebers Ulrich von Rappoltstein war.
      </p>
      <p class="MsoNormal">Da die Aussage
          ber diesen Schreiber  nicht in der ersten, sondern in der dritten Person formuliert
           der Zusammenfassung der Genese der gesamten Handschrift folgt und da ein
          eindeutiges <i>per me</i> fehlt,<a href="#_ftn72" id="_ftnref72"><sup>[72]</sup></a> muss es sich beim
          Eintrag auch nicht unbedingt um den Kolophon des genannten, laut der Forschung
          ersten von zwei Schreibern handeln.<a href="#_ftn73" id="_ftnref73"><sup>[73]</sup></a> Wahrscheinlicher ist,
          dass hier die Schlussbemerkung des Endredaktors der Handschrift vorliegt.
          Ausserdem geht aus dem Kolophon nicht eindeutig hervor, dass der Ungenannte <i>von Onhein</i> einen zweiten Schreiber der Hs. V bezeichnet.<a href="#_ftn74" id="_ftnref74"><sup>[74]</sup></a>
          Es ist nmlich mglich, dass dieser Mann, der die Damen mit seinem ergrauten
          Haar tuscht, lediglich als Kontrast zum jungen <i>Henselin</i> genannt wird.
      </p>
      <p class="MsoNormal">Tatschlich
          lassen sich im Schriftbild von Hs. V nicht nur zwei, sondern insgesamt fnf Hnde
          unterscheiden, die vermutlich zu unterschiedlichen Zeiten am Codex geschrieben
          haben.<a href="#_ftn75" id="_ftnref75"><sup>[75]</sup></a> Von der Schreiberhand I stammt
          das erste Doppelblatt (Bl.
          <router-link to="/faksimile/V/001r">1</router-link>/<router-link to="/faksimile/V/010v">10</router-link>) der
          Handschrift. Dieses umgibt die von Hand II beschriebenen Bltter
          <router-link to="/faksimile/V/002r">2</router-link><router-link to="/faksimile/V/009v">9</router-link> der
          ersten Lage und scheint das ursprngliche Blatt dieser Lage ersetzt zu haben. Hand
          II schrieb zudem die zweite Lage (Bl.
          <router-link to="/faksimile/V/011r">11</router-link><router-link to="/faksimile/V/020v">20</router-link>), die
          auch den Prologus beinhaltet, taucht spter im Codex allerdings nicht mehr auf.
          Von der Schreiberhand III stammt der umfangreichste Teil der Handschrift. Sie
          hat mit wenigen Ausnahmen alle Bltter der 3.35. Lage (Bl.
          <router-link to="/faksimile/V/021r">21</router-link><router-link to="/faksimile/V/274v">274</router-link>)
          beschrieben. Eine weitere Schreiberhand Nr. IV verantwortet den zweiten Teil
          von Buch XVI aus Wolframs Parzival (L. 799.1827.30) mit Einschben, ferner den
          Epilog des Nuwen Parzifal sowie das Explicit der Handschrift (Bl.
          <router-link to="/faksimile/V/312v">312v</router-link>b<router-link to="/faksimile/V/320v">320v</router-link>a). Von
          der Schreiberhand V schlielich, bei der es sich mglicherweise um Hand III in
          anderem Duktus handelt, stammen im gesamten Codex neben dem Kolophon lediglich
          die vermutlich erst beim Abschluss der Handschrift eingetragenen sieben
          Minneliedstrophen auf Bl.
          <router-link to="/faksimile/V/115v">115v</router-link>a/b
          sowie eine Blattberechnung am Schluss von Hs. V (Bl.
          <router-link to="/faksimile/V/320v">320v</router-link>a).<a href="#_ftn76" id="_ftnref76"><sup>[76]</sup></a>
          Zudem korrigierte die Schreiberhand V diverse herausgeschnittene und wieder eingeklebte
          Bltter in der sechsten, siebten und neunten Lage der Handschrift und beschrieb
          das gesamte Bl.
          <router-link to="/faksimile/V/069r">69</router-link>.<a id="_Ref355268284"/><a href="#_ftn77" id="_ftnref77"><sup>[77]</sup></a></p>
