      <p class="MsoNormal">Die Federzeichnungen auf dem
        <router-link to="/faksimile/V/321r">Hinterspiegel</router-link> von Hs. V sind frhestens zu Beginn des 15. Jahrhunderts
          enstanden. In diese Zeit fllt die Entwicklung und Aufnahme in die Heraldik des
          Stechhelm (auch: Krtenkopfhelm) genannten Turnierhelms (137090 entwickelt) sowie
