          zum lteren Topfhelm  ausschweifender Gesichtsschutz den beiden ersten Wappendarstellungen
          auf dem Hinterspiegel als Vorbild diente.<a href="#_ftn101" id="_ftnref101"><sup>[101]</sup></a></p>
