      <h5><a id="_Toc389815994"/><a id="_Ref379015302"/><a id="_Ref379014940"/><a id="_Ref379012129"/><a id="_Ref379010940"/>1.5.1 Die Grafen von Rappoltstein und von Helfenstein als Besitzer von V
      </h5>
      <p class="MsoNormal">ber einen Zeitraum von drei
          Jahrhunderten scheint die Handschrift im Familienbesitz der Grafen von Rappoltstein
          geblieben zu sein, bevor sie vermutlich durch Heirat in den Besitz der Grafen
          von Helfenstein berging.<a href="#_ftn103" id="_ftnref103"><sup>[103]</sup></a> Jedenfalls wurde der
          Codex gem Besitzeintrgen noch im 16. Jahrhundert von Mitgliedern der Familie
          gelesen. In der oberen Hlfte des <router-link to="/faksimile/V/vs001v">Vorderspiegels</router-link>
          finden sich von unterschiedlichen Hnden drei Bcherzeichen mit Devisen,<a href="#_ftn104" id="_ftnref104"><sup>[104]</sup></a>
          welche die Jahresangaben 1552 und 1553 enthalten.<a href="#_ftn105" id="_ftnref105"><sup>[105]</sup></a></p>
      <p class="MsoNormal">                In einem
          Bcherzeichen mit der Devise <i>IVTG</i> (= Ich vermisse Trennung
          gemach)<a href="#_ftn106" id="_ftnref106"><sup>[106]</sup></a> von 1552 werden mit den
          Abkrzungen <i>EFZRapoltein</i> und <i>GGZ helffentein</i> die damals 24-
          oder 25-jhrige <span style="letter-spacing:1.0pt">Elisabeth von
            Helfenstein-Wiesensteig</span> (152784), Witwe des Georg, Freiherr zu
          Rappoltstein (Heirat 1543,  1548), sowie wahrscheinlich ihr neun Jahre lterer
          Bruder <span style="letter-spacing:1.0pt">Georg II., Graf zu Helfenstein</span>
          und Freiherr von und zu Gundelfinden (151873), 1551 Oberst in Siebenbrgen,
          genannt.<a href="#_ftn107" id="_ftnref107"><sup>[107]</sup></a> Unterhalb dieses
          Bcherzeichens steht von anderer Hand eingetragen ein weiteres, das mit der Jahreszahl
          1552 und der Devise <i>H.I.V.M.</i> (= Hoffe immer und [minne?]) versehen ist.<a href="#_ftn108" id="_ftnref108"><sup>[108]</sup></a>
          Bei der darin genannten <i>Barbra Grefin</i> | <i>u helfentain </i>handelt
          es sich um die damals noch unverheiratete, 17- oder 18-jhrige <span style="letter-spacing:1.0pt">Barbara, Grfin zu Helfenstein</span> (153473),
          Schwester der beiden oben Genannten.<a href="#_ftn109" id="_ftnref109"><sup>[109]</sup></a> Als drittes
        Bcherzeichen findet sich auf dem <router-link to="/faksimile/V/vs001v">Vorderspiegel</router-link> rechts des erstgenannten von
          einer weiteren Hand ein Eintrag aus dem Folgejahr 1553, in dem unterhalb der
          Devise <i>MHZG</i> (= Meine Hoffnung [Mein Hoffen] zu Gott)<a href="#_ftn110" id="_ftnref110"><sup>[110]</sup></a>
          die Abkrzung <i>MGZH</i> aufgefhrt ist. <i>MGZH</i> steht entweder fr eine
          Devise (Mit Gott ze Hilfe?) oder fr eine weitere Schwester der bereits
          Genannten, nmlich fr die damals ebenfalls noch unverheiratete, 22- oder
          23-jhrige <span style="letter-spacing:1.0pt">Margareta, Grfin zu
            Helfenstein</span> (15301589).<a href="#_ftn111" id="_ftnref111"><sup>[111]</sup></a></p>
      <p class="MsoNormal">                Die beiden Schreiberhnde
          der Eintrge von 1552 sind zudem verantwortlich fr zwei weitere Bcherzeichen
          mit kurzen Versen im inneren von Hs. V. Von der ersten Schreiberhand stammt
