          <router-link to="/faksimile/V/080v">80v</router-link>, das die
          Verse L. 506.9509.15 aus dem Beginn des zehnten Buchs von Wolframs Parzival enthlt,
          in denen berichtet wird, wie der Ritter Gawan das erste Mal auf seine knftige
