          Auf dieselbe Schreiberhand geht ein ebenfalls Gawan betreffender Eintrag am unteren Rand der ersten Spalte zurck, der folgende gereimte Verse
          enthlt:
      </p>
      <p class="verse"><i>Gabwan </i>[oder:<i> Gawan</i>]<i>
            der <span class="titel1"></span>chen<span class="titel1"></span>t Edle<span class="titel1"></span>t riter | got</i></p>
      <p class="verse">
