          Reime eingetragen, die auf jene Episode anspielen, in der Karados durch Gyngenier von der Schlange an seinem
          Handgelenk erlst wird (NP 66516732; Sch. 150.35152.25):
      </p>
      <p class="verse">
                          <i>Riter Karits vnd <span class="titel1"></span>inge:ein |</i>
      </p>
      <p class="verse">
