      <p class="verse">
                          <i>bis jer got halff | das <span class="titel1"></span> jn jm klo<span class="titel1"></span>ter fand</i>
      </p>
      <p class="verse">
            <i>vnd jn erle<span class="titel1"></span>t | vom erband</i><a href="#_ftn117" id="_ftnref117"><sup>[117]</sup></a>
