      </p>
      <h5><a id="_Toc389815995"/>1.5.2 Handschrift V in der Frstlich Frstenbergischen Bibliothek in Donaueschingen</h5>
      <p class="MsoNormal">ber Elisabeth von Helfenstein-Wiesensteig (152784), seit 1543 Ehefrau Georgs, des Freiherrn zu
          Rappoltstein, kam die Handschrift in Helfensteinischen Besitz. Nach dem
          Tod Georgs im Jahr 1548 heiratete Elisabeth ins Haus Frstenberg ein. Dessen
          Graf Wratislaus II. (in erster Ehe mit Johanna Eleonora von Helfenstein-Gundelfingen,
          in zweiter mit Franziska Caroline von Helfenstein-Wiesensteig verheiratet) erwarb
          nach dem Aussterben des Mannesstammes der Helfensteiner mit den Besitztmern
          auch die Bibliothek der Grafen von Helfenstein-Wiesensteig, die er in die
          Bchersammlung in Mekirch inkorporierte. Mit der Zusammenfhrung aller
          Teilbibliotheken gelangte im Jahr 1768 auch Hs. V in den neu errichteten
          Bibliotheksraum der Frstlich Frstenbergischen Hofbibliothek in Donaueschingen
          (schwarzer Stempel auf den Bll.
          <router-link to="/faksimile/V/001r">1r</router-link> und
          <router-link to="/faksimile/V/320v">320v</router-link>: <i>HOCHF
             FRSTENB  BIBLIOTHEKE +</i>).<a href="#_ftn118" id="_ftnref118"><sup>[118]</sup></a></p>
      <p class="MsoNormal">                Erstmals ausdrcklich
          erwhnt wird die Aufbewahrung der Handschrift mit der Signatur <i>37.9.</i>
        (Eintrge mit schwarzer Tinte auf dem <router-link to="/faksimile/V/vs001v">Vorderspiegel</router-link>)
          in der Frstlich Frstenbergischen Bibliothek in Donaueschingen von <span style="font-variant:small-caps">Ludwig Uhland</span> im Taschenbuch fr Geschichte
          und Alterthum in Sddeutschland im Jahr 1840, wo er den Codex als unverkennbare(n)
          Doppelgnger von Hs. V' vorstellt und die acht mittelhochdeutschen Minneliedstrophen
          daraus verffentlicht.<a href="#_ftn119" id="_ftnref119"><sup>[119]</sup></a> In der
          Bestandsaufnahme der deutschsprachigen Handschriften durch den Bibliothekar <span style="font-variant:small-caps">Joseph Victor Scheffel</span> vom Jahr 1859 ist
          der Codex unter der Rubrik Deutsche Volks- und Heldensage
          nur kurz mit der Laufnummer IX. aufgefhrt.<a href="#_ftn120" id="_ftnref120"><sup>[120]</sup></a> Bei der Neuordnung und
          Katalogisierung der Handschriften durch den Amtsnachfolger <span style="font-variant:small-caps">Karl August Barack</span> im Jahr 1864 hat
          er die Signatur 97 erhalten.<a href="#_ftn121" id="_ftnref121"><sup>[121]</sup></a></p>
      <h5><a id="_Toc389815996"/><a id="_Ref379017708"/>1.5.3 Aufbewahrung von Handschrift V in der Badischen Landesbibliothek in Karlsruhe</h5>
      <p class="MsoNormal">Beim Verkauf der Sammlung der Frstlich Frstenbergischen Hofbibliothek an das Land Baden-Wrttemberg im Jahr 1993
          ist die Handschrift zusammen mit dem Groteil der deutschsprachigen Manuskripte
          aus der Zeit bis ca. 1500 in die Badische Landesbibliothek in
          Karlsruhe gelangt, wo sie heute unter der Signatur <i>Donaueschingen 97</i> (Eintrge auf dem <router-link to="/faksimile/V/vs001v">Vorderspiegel</router-link>
          mit Bleistift und Tinte: <i>Don 97</i>) <a href="#_ftn122" id="_ftnref122"><sup>[122]</sup></a>
          aufbewahrt wird.
      </p>
      <h3><a id="_Toc389815997"/>2 Kopie: Handschrift V'  Roma, Biblioteca Casanatense, Ms. 1409
      </h3>
      <p class="MsoNormal">Mindestens einmal ist Hs. V an
          einem nicht bekannten Ort abgeschrieben worden. Bei der nicht nach Diktat, sondern mittels
          einer Vers-fr-Vers-bertragung<a href="#_ftn123" id="_ftnref123"><sup>[123]</sup></a> erstellten Kopie V'
          handelt es sich mit hoher Wahrscheinlichkeit um eine direkte Abschrift von Hs. V
          ohne Zwischenstufen. Als Indizien fhrt <span style="font-variant:small-caps">Stolz</span> das hnliche Layout der Erffnungsseiten, die Verfahren der Initialsetzung, markierte Stellen, verschiedene Vermerke, die vom Schreiber von Hs. V' stammen knnten, sowie dialektale Standardisierungen an.<a href="#_ftn124" id="_ftnref124"><sup>[124]</sup></a> Zudem weisen folgende Anzeichen darauf hin, dass Hs. V' direkt von Hs. V abgeschrieben worden ist: (1) Im Rappoltsteiner Florilegium (vgl. oben Kap. <a href="#_Toc389815981">1.2.3</a>) hat Schreiber I von Hs. V' in Vers 31 der Minneliedstrophen auf Bl. <router-link to="/faksimile/VV/001r">1r</router-link>a einen in Hs. V unbeabsichtigten Abstand zwischen den ersten zwei Buchstaben von <i>betwungen</i> bernommen. (2) Den Versal <i>M</i> von <i>Mere</i> in L. 754.9 (Hs. V, Bl. <router-link to="/faksimile/V/305r">305r</router-link>b) hat Schreiber II beim Abschreiben wegen der Rubrizierung verlesen zu <i>H</i> (Bl. <router-link to="/faksimile/VV/173r">173r</router-link>a). (3) In Vers 116 des Epilogs erscheint in Hs. V' (Bl. <router-link to="/faksimile/VV/180r">180r</router-link>b) wie in Hs. V (Bl. <router-link to="/faksimile/V/318r">318r</router-link>b) der Versal <i>t</i> als Minuskel, obschon in beiden Codices in der Umgebung alle Versalien mit Majuskeln geschrieben sind.
      </p>
      <p class="MsoNormal">Die vielen, zum Teil
          sehr umfangreichen Krzungen (um die 5300 Minusverse gegenber Hs. V)
          sind nachweislich bei der Abschrift des Codex erfolgt.<a href="#_ftn125" id="_ftnref125"><sup>[125]</sup></a> Darauf deuten graphische Relikte hin, die bei Tilgungsmanahmen stehen geblieben sind: So hat Schreiber II ein auf der letzten Zeile von Bl. <router-link to="/faksimile/VV/144r">144r</router-link>a und der ersten Zeile von Bl. <router-link to="/faksimile/VV/144r">144r</router-link>b von Hs. V' eingetragenes Verspaar zunchst kopiert, dann aber wieder ausradiert, da es am Beginn eines gegenber der Vorlage bersprungenen Absatzes steht (NP 2952629589, die ersten beiden, nachtrglich eliminierten Verse sind nur unter UV-Licht lesbar und hier diplomatisch wiedergegeben: <i>A: dem morgen <span class="titel1"></span>ie :it enpitten | Balde ire <span class="titel1"></span>tra<span class="titel1"></span>e <span class="titel1"></span>ie riten).</i> Den Rest einer Tilgung enthlt auch der vorletzte auf Bl. <router-link to="/faksimile/VV/170v">170v</router-link>a eingetragene Vers (NP 36203), wo der Schreiber das Possessivpronomen <i>Siner</i> aus der Vorlage zunchst kopiert hat, ehe er sich fr eine Krzung entschied und das bereits geschriebene Wort wieder strich, um mit einer eigenen Formulierung fortzufahren.
      </p>
      <p class="MsoNormal">
          Die von <span style="font-variant:small-caps">Karin Schneider</span> gem
          palographischem Befund auf das sptere zweite Viertel des 14. Jahrhunderts
          datierte Hs. V'<a href="#_ftn126" id="_ftnref126"><sup>[126]</sup></a> enthlt lediglich den
          zweiten Teil des Rappoltsteiner Parzifal, d.h. den Nuwen Parzifal, ergnzt durch die beiden letzten Bcher XV und XVI von Wolframs Parzival.<a href="#_ftn127" id="_ftnref127"><sup>[127]</sup></a></p>
      <h4><a id="_Toc389815998"/><a id="_Ref379010666"/>2.1 Beschreibung der Handschrift V'</h4>
      <h5><a id="_Toc389815999"/>2.1.1 uere Beschreibung</h5>
      <p class="MsoNormal" style="text-indent:.1pt"><b>Rappoltsteiner Parzifal (nur zweiter Teil): Minneliedstrophen; Klaus Wisse und Philipp Colin,
          Nuwer Parzifal (stark gekrzt)</b>
      </p>
      <p class="MsoNormal">Entstanden innerhalb einer Epenwerkstatt mit Wolfram-Texten      Pergament, 3 (Papier) + 181 + 1 (Papier) Bll.      39 x 27,5  cm      Raum Franken      2. Viertel 14. Jh.</p>
      <p class="MsoNormal">Blattzhlung: III + 181 Bll. + I Bl. Moderne Paginierung (oben rechts) in Drucktypen
          <i><router-link to="/faksimile/VV/001r">1</router-link><router-link to="/faksimile/VV/181r">181</router-link> </i>
          (wird im Folgenden zitiert). Alte Bleistiftzhlung (oben rechts)
          <i><router-link to="/faksimile/VV/001r">1</router-link><router-link to="/faksimile/VV/181r">182</router-link> </i>
          (springt von <i><router-link to="/faksimile/VV/071r">71</router-link></i> auf <i><router-link to="/faksimile/VV/072r">73</router-link></i>).
          Pergament unterschiedlicher Dicke mit Lchern (genht). Vordere und hintere Vorsatzbltter
          aus Papier des 16. Jahrhunderts mit Wasserzeichen Anker im Kreis, von Stern berhht, Abstand der beiden ueren von drei Bindedrhten 60 mm,
          sehr hnlich Briquet Online Nr. <a href="http://www.ksbm.oeaw.ac.at/_scripts/php/BR.php?refnr=488" target="_blank">488</a> (Ferrara 1583).
      </p>
      <p class="MsoNormal"><b>Einband</b>: Zwischen 1701 und 1800 entstandener, 40,5 x 28 cm groer <router-link to="/faksimile/VV/vs001r">Einband</router-link> (Kartondeckel mit hellem Schweinsleder
        bezogen) ohne Verzierung (<router-link to="/faksimile/VV/vs001r">Vorderdeckel</router-link> und <router-link to="/faksimile/VV/183v">Hinterdeckel</router-link> identisch). 5,56,0 cm breiter
        <a :href="figure('VV', '184r')">Rcken</a> mit
          sieben Bnden. Im ersten Feld: <i>A.I. | I</i> (mit schwarzer Tinte), im zweiten Feld: <i>Parcefali Varior</i>[um]<i>
          | Rerum Natural</i>[ium]<i> | POEMA</i> (mit schwarzer Tinte, mit Bleistift unterstrichen), im
          vierten Feld die Ziffer <i>19</i> (mit schwarzer Tinte). Den <router-link to="/faksimile/VV/vs001v">Vorderspiegel</router-link> bildet das innen auf den Deckel
          geklebte erste Blatt eines als Vorsatzbltter verwendeten Binio aus Papier, das Ende des 16. Jahrhunderts in Italien hergestellt worden ist
          (siehe oben). Auf dem <router-link to="/faksimile/VV/vs001v">Vorderspiegel</router-link> eingetragen finden sich jeweils mit schwarzer Tinte die alten Signaturen: <i>AR. I. 9.</i>
          (mit schwarzer Tinte doppelt durchgestrichen), darunter <i>I A. I. 19.</i> (die erste Ziffer <i>I</i> mit schwarzer Tinte doppelt durchgestrichen) und <i>Cod.
            1409.</i> (mit anderer Tinte). Rechts neben diesen drei Signaturen eine Marke (3,7 x 3,7 cm) mit der Aufschrift:
          <i>BIBLIOTECA</i> (horizontal oben); <i>ROMA</i> (horizontal unten); <i>REGIA</i> (vertikal links); <i>CASANATENSE</i> (vertikal rechts).
          Im Kreis: <i>Mss.</i>, darunter mit Stempel die Signatur <i>1409</i>. Eine identische, beschdigte Marke, die ursprnglich
          im ersten Feld des <a :href="figure('VV', '184r')">Rckens</a> aufgeklebt war,
            liegt der Hs. bei. Auf der <router-link to="/faksimile/VV/vs004r">Rectoseite</router-link> des letzten, dritten Blattes des Binio aus Papier mit Tinte
          eine Inhaltsangabe des 18. Jahrhunderts: <i>Paracefali</i> [!] | <i>Poema lingua Germanica</i>, am unteren Blattrand mit Bleistift die
            Ziffer <i>I</i>. Den <router-link to="/faksimile/VV/183r">Hinterspiegel</router-link> bildet das zweite Blatt eines als hintere Vorsatzbltter verwendeten Unio desselben Papiers.
      </p>
