            zum Nuwen Parzifal mit Jahresangabe 1336 </b>(NP 0-1<sup>2</sup>; Sch. XIII):
          <i>&lt;<b>N</b>v ge<span class="titel1">wigen wir kvnig artues | hie. vnd
                agent von hern Ga=|wane </span> <span class="titel1">Des
                inn mer it | danne der dvtche parzefal. der nv lange | getihtet it.&gt;</span></i></div>
      <div style="clear:both"/>
      <div class="contentbox-left"/>
      <div class="contentbox-right"><i>&lt;Vn(d) alles
                daz hie nach gechri=|ben tat. daz it ouch parcifal. vnd it | von welche zv
                dvtche braht. vn(d) volle | tihtet. vn(d) zv ende braht. Daz gechach | do
                man zalte von gotes gebvrte drizen | hundert iar. vn(d) drizzig iar. in dem
                ehte(n) | iare.&gt;</i>
      </div>
