      <h4><a id="_Toc389816001"/><a id="_Ref379010758"/>2.2 Genese der Handschrift V'
      </h4>
      <p class="MsoNormal">Im Gegensatz zu den Entstehungszusammenhngen von Hs. V
          war die Genese von Hs. V' lange Zeit ungeklrt. Erst der
          Nachweis, dass einer der Schreiber mit dem
          Hauptschreiber einer bekannten, im nordbairisch-frnkischen Raum ttigen Werkstatt identisch ist,
          hat einen Einblick in den Entstehungskontext von Hs. V' ermglicht.<a href="#_ftn130" id="_ftnref130"><sup>[130]</sup></a>
      </p>
      <h5><a id="_Toc389816002"/>2.2.1 Schreiber</h5>
      <p class="MsoNormal">Auf die Lagen verteilt waren zwei Schreiber<a href="#_ftn131" id="_ftnref131"><sup>[131]</sup></a> wie folgt ttig:</p>
