      <p class="MsoNormal">Der erste der beiden am Codex beteiligten Schreiber hat die ersten sechs Lagen beschrieben und diese jeweils mit rmischen Ziffern nummeriert. Von ihm stammen auch das Initium der
          Eingangsseite in roter Tinte (Bl. <router-link to="/faksimile/VV/001r">1r</router-link>a) sowie die Reprsentanten der Lombarden. Auch die vier ersten der roten berschriften (auf Bl. <router-link to="/faksimile/VV/003r">3r</router-link>a,
          <router-link to="/faksimile/VV/004r">4r</router-link>a, <router-link to="/faksimile/VV/006r">6r</router-link>a und
          <router-link to="/faksimile/VV/008v">8v</router-link>b) sind von dieser Schreiberhand verantwortet, die
          wahrscheinlich auch die bis Bl. <router-link to="/faksimile/VV/008v">8v</router-link> reichende Rubrizierung ausgefhrt hat.
          Auf Bl. <router-link to="/faksimile/VV/049r">49r</router-link>a hat dann auf einer neuen Lage der zweite Schreiber eingesetzt, den restlichen Text kopiert,
          die Lagen mit rmischen Ziffern nummeriert und wohl seinerseits die Reprsentanten vorgezeichnet. Im Anschluss an den Texteintrag sind fr die gesamte Handschrift die
          roten und blauen Lombarden und danach die berschriften angefertigt worden; zumindest letztere stammen ebenfalls von der zweiten Schreiberhand.
      </p>
      <p class="MsoNormal">                Ein fundierter
          Schriftvergleich hat ergeben, dass es sich bei der
          Schreiberhand I um die Haupthand eines im 14. Jahrhundert ttigen Skriptoriums handelt,
          aus dem eine beachtliche Anzahl von Handschriften erhalten ist.<a href="#_ftn132" id="_ftnref132"><sup>[132]</sup></a> Die Charakterisierung der Textualis formata des sogenannten Epenschreibers
          durch <span style="font-variant:small-caps">Karin Schneider</span> entspricht
          mit wenigen, begrndbaren Ausnahmen derjenigen dieses ersten Schreibers von Hs. V'.<a href="#_ftn133" id="_ftnref133"><sup>[133]</sup></a>
          Die zweite Schreiberhand von V' kann in diesem Skriptorium hingegen nicht nachgewiesen
          werden.
      </p>
      <h5>
                          <a id="_Toc389816003"/>
                          <a id="_Ref379124988"/>
                          <a id="_Ref379124893"/>2.2.2 Buchschmuck
      </h5>
      <p class="MsoNormal">Auch die Eingangsinitiale <i>W</i> mit blau-rot gespaltenem
          Buchstabenstamm (Bl. 1ra) sowie weitere Initialen knnen denjenigen in
          Handschriften des genannten Skriptoriums zugewiesen werden.<a href="#_ftn134" id="_ftnref134"><sup>[134]</sup></a>  Allerdings lsst sich der Maler der zweifarbigen Fleuronne-Initialen <i>W</i> und <i>H</i> auf der Eingangsseite (Bl. <router-link to="/faksimile/VV/001r">1r</router-link>) nicht nher bestimmen.</p>
      <h5>
