        Texte). So beinhaltet diese Sammlung Wolframs von Eschenbach Parzival (Pz.), Albrechts Jngeren Titurel (JT), den Lohengrin (Loh.), den Willehalm (W-Will.),
        die Arabel Ulrichs von dem Trlin (Arab.) sowie den Rennewart Ulrichs von Trhheim (Renn.). Der Epenwerkstatt knnen zudem drei
