      <p class="MsoNormal">Trotz der offenkundigen bereinstimmungen im Bereich der
          Schreiberhnde, der Initialornamentik sowie des Schreibdialekts
          kann dieses Skriptorium nicht genauer bestimmt werden.<a href="#_ftn137" id="_ftnref137"><sup>[137]</sup></a>
          Auch sind keine Rckschlsse auf die Herstellungs- und Bestimmungsorte der Codices sowie
          auf die Auftraggeber mglich: Die erwhnten Handschriften
          sind entweder nur bruchstckhaft erhalten oder sie wurden vor der berfhrung
          der Heidelberger Palatina nach Rom ihrer Einbnde und ursprnglichen
          Vorsatzbltter entledigt. Die in der Forschung wiederholt diskutierte Frage, ob
          es sich bei der Epenwerkstatt um ein klsterliches Skriptorium gehandelt hat,
          muss ebenfalls offen bleiben.<a href="#_ftn138" id="_ftnref138"><sup>[138]</sup></a></p>
      <h4><a id="_Toc389816005"/><a id="_Ref379010814"/>2.3 <span class="MsoPageNumber">Besitzgeschichte</span> der Handschrift V'
      </h4>
      <p class="MsoNormal">Anders als bei Hs. V, dem Codex
          Donaueschingen 97, lsst sich die Besitzgeschichte des Codex Casanatensis somit nicht
          lckenlos nachgezeichnen. Sie ist erst ab dem 16. Jahrhundert mit
          Sicherheit rekonstruierbar. ber den frheren Verbleib von Hs. V' und ber
          deren Besitzer knnen lediglich Mutmaungen angestellt werden.
      </p>
      <h5><a id="_Toc389816006"/>2.3.1 Lamprecht von Brun als Besitzer von Handschrift V'?</h5>
      <p class="MsoNormal">Als frher Besitzer der Sammlung,
          zu der auch Hs. V' gehrt hat, wird der um 1320/30 geborene und 1399 verstorbene
          Bamberger Bischof Lamprecht (Lampertus, Lambert) von Brun (von Brunn, von Burn,
          de Buren, von Buren, de Fonte) vermutet.<a href="#_ftn139" id="_ftnref139"><sup>[139]</sup></a> Grund fr diese Annahme
          ist der Umstand, dass im Jahr 1408 als Schenkung Teile aus dessen
          Bchersammlung (<i>etweuil Pucher</i>) in die Heidelberger Universitt gelangt sind,
          die  wie testamentarisch verordnet  zugunsten des von Lamprecht 1395
          gestifteten Spitals in Schelitz fr 350 Gulden an den rmischen Knig
          Ruprecht von der Pfalz verkauft worden waren.<a href="#_ftn140" id="_ftnref140"><sup>[140]</sup></a>
          Lamprechts Anniversarium vom 5.5.1399 erwhnt einige Bcher (<i>certos libros</i>), die zuvor als Schenkung ins 1354 durch
          die Bamberger Bischfe errichtete Chorherrenstift St. Martin zu Forchheim (Erzdizese
          Bamberg) bergegangen waren,<a href="#_ftn141" id="_ftnref141"><sup>[141]</sup></a> wo es allerdings keine
          Bibliothek, die diesen Namen verdienen wrde, gab.<a href="#_ftn142" id="_ftnref142"><sup>[142]</sup></a>
          Lamprechts heute verstreute, laut <span style="font-variant:small-caps">Joseph Heller</span> sehr bedeutende
          Bibliothek<a href="#_ftn143" id="_ftnref143"><sup>[143]</sup></a> ist ber die Jahre
          durch Beziehungen und durch seine Funktionen an unterschiedlichen
          Wirkungssttten angewachsen.
      </p>
      <p class="Standartpetit">Der aus einem elsssischen niederadeligen Geschlecht
          stammende Lamprecht von Brun trat frh in das Benediktinerkloster Neuweiler
          (Elsass) ein, wo er bis 1350 das Amt des Camerarius bekleidete. 134678 war er
          zudem Kanzler Kaiser Karls IV. und wurde 1354 oder 1356 Abt des
          Benediktinerklosters Gengenbach (Ortenaukreis, Baden-Wrttemberg). In den
          Jahren 135365 war er Hofkaplan des Straburger Frstbischofs Johann II. und
          wurde um 1360 Bischof von Brixen (Italien), 136471 von Speyer und 137175 von Straburg. Zuletzt war er ber zwanzig Jahre Bischof von Bamberg
          (137498) und nach dem Tod Kaiser Karls IV. im Jahr 1379 Berater und
          vorbergehend Kanzler Knig Wenzels, bis er 1399 schlielich resignierte und
          sich ins Chorherrenstift in Forchheim zurckzog, wo er im selben Jahr am 17.
          Juli verstarb.<a href="#_ftn144" id="_ftnref144"><sup>[144]</sup></a> Bereits im
          Benediktinerkloster Neuweiler besa Lamprecht von Brun eine vermutlich in
          Bologna im zweiten Viertel des 14. Jahrhunderts geschriebene Dekretalenhandschrift
          mit dem Apparatus in quinque libros decretalium Innocents IV.<a href="#_ftn145" id="_ftnref145"><sup>[145]</sup></a>
          Eine weitere, ebenfalls aus Oberitalien stammende Dekretalenhandschrift aus dem
          vierten Viertel des 13. Jahrhunderts kaufte Lamprecht vermutlich in Gengenbach.<a href="#_ftn146" id="_ftnref146"><sup>[146]</sup></a>
          In seiner Bamberger Zeit erwarb er zudem wahrscheinlich die Bcher seines
          Vorgngers Friedrich I. von Hohenlohe (Regierungszeit bis 1352), von denen sich
          ein 1348 geschriebenes Urbar der Bamberger Kirche (<span lang="IT"></span>Liber
          possessionum<span lang="IT"></span>)<a href="#_ftn147" id="_ftnref147"><sup>[147]</sup></a> sowie Teile einer im
          selben Jahr fertig gestellten Handschrift erhalten haben (darin die Vita Heinrici II.
          imperatoris des Adalbertus, die Miracula sancte Chunegundis mit Gebet sowie
           als spterer Nachtrag  die Liturgica, eine Sammlung von textierten
          Melodien mit Versen zu den Festen Heinrichs und Kunigundes sowie des Hl.
          Benedikt). Diese Handschrift gelangte spter in die Bestnde der
          Heidelberger Palatina und wurde 1622/23 nach Rom gebracht, wo sie mit einer
          Armenbibel des 15. Jahrhunderts zusammengebunden wurde.<a href="#_ftn148" id="_ftnref148"><sup>[148]</sup></a>
          In der Bibliothek Lamprechts von Brun befanden sich mglicherweise auch zwei
          in der zweiten Hlfte des 14. Jahrhunderts entstandene medizinische Sammelhandschriften
          aus dem Besitz des vor 1390 verstorbenen Magisters Reimbotus (de Castro),
          des Leibarzts Karls IV., darunter <span lang="IT">Johannes des Gaddesden Rosa
            anglica medicinae aus dem Jahr 1368.</span><a href="#_ftn149" id="_ftnref149"><sup>[149]</sup></a></p>
      <p class="MsoNormal">Bei den berlieferten Codices,
          die  mit Vorbehalt  als Lamprechts von Brunn Besitz ausgewiesen werden
          knnen, handelt es sich um Handschriften mit liturgischen, kirchenrechtlichen
          und medizinischen Texten. Hingegen fehlt ein eindeutiger Hinweis darauf, dass der Bischof von Bamberg an
          volkssprachigen Epen interessiert gewesen wre und die in der Epenwerkstatt entstandenen
          Codices besessen oder gar in Auftrag gegeben htte.
      </p>
      <h5><a id="_Toc389816007"/><a id="_Ref381895864"/><a id="_Ref379125906"/>2.3.2 Handschrift V'  der Cod. Pal. germ. 317 der Heidelberger Palatina</h5>
      <p class="MsoNormal">Wie zuerst <span style="font-variant: small-caps">Matthias Miller</span> 2007 bemerkt hat, zhlte Hs. V' sptestens
          ab 1581 bis 1623 zum Bestand der Heidelberger Palatina, wo sie unter der
          Signatur Cod. Pal. germ. 317 aufbewahrt wurde.<a href="#_ftn150" id="_ftnref150"><sup>[150]</sup></a>
          In einem Eintrag im lteren Katalog der Heidelberger Schlossbibliothek von 1581<a href="#_ftn151" id="_ftnref151"><sup>[151]</sup></a> erscheint Hs. V'
          [Nr. 3] zusammen mit den aus der Epenwerkstatt stammenden Heidelberger Codd.
          Pal. germ. <a href="http://handschriftencensus.de/2678" target="_blank">364</a> [Nr. 2], <a href="http://handschriftencensus.de/2682" target="_blank">383</a> [Nr. 4] und <a href="http://handschriftencensus.de/2684" target="_blank">404</a> [Nr. 6]:
      </p>
      <p class="Standartpetit" style="margin-bottom:0cm;margin-bottom:.0001pt">Gechriebene
          Bucher | REGAL vnd Median
      </p>
      <p class="Standartpetit" style="margin:0cm;margin-bottom:.0001pt">[Nr. 1] von
          Herr lantlott, konig Arts todt, Herrn Hector | vnd Gallin. gechrieben Perment,
          brett, chwart | leder bucklen
      </p>
      <p class="Standartpetit" style="margin:0cm;margin-bottom:.0001pt">[Nr. 2]
          Parcefall vnd Lorangrin, alte reymen. gechriben perm.| gleiches bunds<a href="#_ftn152" id="_ftnref152"><sup>[152]</sup></a></p>
      <p class="Standartpetit" style="margin:0cm;margin-bottom:.0001pt">[Nr. 3] Der Welch
          Parcefall vnd Galein. Permet reime. | bret. braun led(er)<a href="#_ftn153" id="_ftnref153"><sup>[153]</sup></a></p>
      <p class="Standartpetit" style="margin:0cm;margin-bottom:.0001pt">[Nr. 4] Der
          Titterel. gechrieben Perment. gleiches bunds<a href="#_ftn154" id="_ftnref154"><sup>[154]</sup></a></p>
      <p class="Standartpetit" style="margin:0cm;margin-bottom:.0001pt">[Nr. 5] Genealogia
          Chriti. gechrieben Permeet. brett. rott leder.
      </p>
      <p class="Standartpetit" style="margin-top:0cm">[Nr. 6] Margrae Wilhelms von
          Cranit bch. Reimen | gechrieben Perment. gleiches bnds.<a href="#_ftn155" id="_ftnref155"><sup>[155]</sup></a></p>
      <p class="MsoNormal">Im Jahr 1623 wurde
          der Codex whrend des Dreiigjhrigen Kriegs ohne Einband mit dem Groteil der Heidelberger Palatina in
          196 Kisten auf Maultieren in die Biblioteca Apostolica Vaticana im Vatikan
          gebracht. Die Hs. V' gelangte getrennt von den anderen Epenhandschriften in der
          Kiste mit der Capsanummer <i>C. 91</i> (siehe Bl.
          <router-link to="/faksimile/VV/001r">1r</router-link>,
          oberer Rand) nach Rom, wo sie die Signatur <i>1248</i> (siehe Bl.
          <router-link to="/faksimile/VV/001r">1r</router-link>,
          oberer Rand) erhielt<a href="#_ftn156" id="_ftnref156"><sup>[156]</sup></a> und um die Mitte des
          17. Jahrhunderts neu gebunden wurde.<a href="#_ftn157" id="_ftnref157"><sup>[157]</sup></a> Als Vorsatzbltter
          eingebunden wurde bei dieser Neubindung italienisches Papier vom Ende des
          16. Jahrhunderts, das vermutlich auch fr die Bindung anderer Handschriften aus der
          Heidelberger Palatina Verwendung fand.<a href="#_ftn158" id="_ftnref158"><sup>[158]</sup></a> Bis in die zweite Hlfte
          des 18. Jahrhunderts blieb Hs. V' mit den anderen Codices aus der Palatina
          in der Vaticana.<a href="#_ftn159" id="_ftnref159"><sup>[159]</sup></a></p>
      <h5><a id="_Toc389816008"/>2.3.3 Heutiger Standort von Handschrift V': Die Biblioteca Casanatense in Rom</h5>
      <p class="MsoNormal">Erst im Jahr 1816 gelangten nach
          mehreren Anfragen 848 deutschsprachige Handschriften der Palatina (<i>Codices Palatini
            germanici</i>), die zu diesem Zeitpunkt noch in der Vaticana aufbewahrt wurden,
          zurck nach Heidelberg in das Universittshauptgebude (<i>Domus Wilhelmiana</i>).
          Unter diesen Codices waren auch die in der Epenwerkstatt gefertigten
          Heidelberger Codd. Pal. germ. <a href="http://handschriftencensus.de/2678" target="_blank">364</a>,
          <a href="http://handschriftencensus.de/2682" target="_blank">383</a> und <a href="http://handschriftencensus.de/2684" target="_blank">404</a>. Hs. V' hingegen wurde
          damals bereits nicht mehr in der Vaticana aufbewahrt. Unter nicht bekannten
          Umstnden war dieser Codex zwischen 1751 und 1792 in die im Jahr 1701 neu
          erffnete Bibliothek des Rmischen Dominikanerkonvents bei Santa Maria sopra
          Minerva, der heutigen Biblioteca Casanatense, gelangt. Dort war er am rechten Rand von Bl.
          <router-link to="/faksimile/VV/001r">1r</router-link> mit einem Bibliotheksstempel versehen worden,
          der einen Turm zeigt und im Uhrzeigersinn gelesen die Inschrift trgt:
          <i>BIBLIOTHE  A CARD  H  CASAN  D  O  PRD </i>. Ein Abklatsch dieses Stempels
          auf der Rckseite des letzten vorderen Spiegelblatts zeigt,
          dass die Handschrift bei der Stempelung bereits eingebunden war.
          In der Biblioteca Casanatense erhielt die Handschrift zunchst die
          Signatur <i>0 19</i> (Nachtrag des ab 1759 amtierenden Bibliothekars
          Giovanni Battista Audiffredi im Katalog von 1744),
          spter <i>AR. I. 9.</i>, dann <i>A. I. 19.</i> und schlielich die heute gltige, am Vorderspiegel angebrachte Signatur <i>Cod. 1409</i>.<a href="#_ftn160" id="_ftnref160"><sup>[160]</sup></a></p>
      <p class="MsoNormal">In der
          Biblioteca Casanatense ist die Forschung zu Anfang des 19. Jahrhunderts erstmals auf den Nuwen Parzifal aufmerksam geworden, dies in der Gestalt der Kopie von Hs. V'. In einem Brief vom
          15. und 16. November 1818 berichtet <span style="font-variant:small-caps">Friedrich
            Heinrich von der Hagen</span> ber den Fund einer deutschsprachigen Handschrift
          in der ehemaligen Dominikanerbibliothek in Rom Folgendes (und belegt dabei seine Kenntnis der franzsischen Quelle, nicht jedoch der Vorlage V):
      </p>
      <blockquote class="Blockquote">In dem handschriftlichen Verzeichnisse der Handschriften fand ich eine einzige
          Deutsche: ich lie sie mir bringen, und siehe, es war ein schwerer Foliant, und
          darin noch ein ganz unbekanntes Altdeutsches Gedicht, nmlich der <span style="letter-spacing:1.0pt">Parcival</span> mit der <span style="letter-spacing: 1.0pt">Tafelrunde</span>. Es ist aber, wie Anfang und Ende besagt, eine Bearbeitung
          des Franzsischen (Prosa) Romanes von <span style="letter-spacing:1.0pt">Manessier</span>
          (hiee [!] <span style="letter-spacing:1.0pt">Manescher</span> genannt), welcher
          dem Gedichte des <span style="letter-spacing:1.0pt">Christian</span> von <span style="letter-spacing:1.0pt">Troyes</span> folgte, und wovon ich den alten
          Druck in Zrich getroffen hatte.<a href="#_ftn161" id="_ftnref161"><sup>[161]</sup></a>
      </blockquote>
