                  denen ein Herrscher verpflichtet ist. Die <i>largesce</i> wurde bereits in der
                  altfranzsischen Dichtung des 12. Jahrhunderts personifiziert; vgl. <span style="font-variant:small-caps">Herzhoff</span> (1904), S. 30f. (Nr. 15).</p>
          </div>
