                  (2003), S. 3336.</p>
          </div>
          <div id="ftn20">
            <p class="MsoFootnoteText"><a href="#_ftnref20" id="_ftn20"><sup>[20]</sup></a><i> Der</i> [sc. <i>chilte</i>]<i> it o rich
              geviieret, | Daz in nieman vberzieret | Daz velt it von fine(n) berlin groz |
              Von Origent wier denne ein loz | Dar vz gent drie chilte irn chin |
              Jeclicher it ein robin | Geliffen reine von origent | Die rehte
              karfunkel chin gent | Fr rot brinnent v dar inne</i> (Ep 129137; Sch. 848.3543). Die
                  Beschreibung entspricht der Wappendarstellung (bis 1485 ungeteilt, danach
                  viergeteilt; vgl. <span style="font-variant:small-caps">Zeilinger</span>
                  [2012a], S. 1151) auf dem Siegel Anselms von Rappoltstein (vgl. die Abbildung auf den Titelblttern
                  der Bnde des <a href="http://gallica.bnf.fr/ark:/12148/bpt6k94010463/f15.image.r=.langDE" target="_blank">RUB</a>: Rappoltsteinisches Urkundenbuch [18911898]) sowie auf
                  der um 13301345 im Bodenseeraum entstandenen Zrcher Wappenrolle (Zrich,
                  SNM, AG 2760, Bl. <a href="https://www.e-codices.unifr.ch/de/snm/AG002760/3v/0/Sequence-1329" target="_blank">3v</a>, untere
                  Reihe, achtes Wappen von links; vgl. das Volldigitalisat auf <a href="http://www.e-codices.unifr.ch/de/snm/AG002760/3v/0/Sequence-1329" target="_blank">e-codices.ch</a>.</p>
          </div>
          <div id="ftn21">
            <p class="MsoFootnoteText"><a href="#_ftnref21" id="_ftn21"><sup>[21]</sup></a> Vgl. <span style="font-variant:small-caps">Backes</span> (2009), S. 5. 
            Im Minnebrief der <i>Minne</i> an das Herz Ulrichs von Rappoltstein heit es: <i>kvnig artus
                      mute din mog in | Wan er och ine tu(n)de | Do mitte kvrzen begunde | Daz
                      er leendez ich gewag</i> (Ep 210213; Sch. 850.2427).</p>
          </div>
          <div id="ftn22">
            <p class="MsoFootnoteText"><a href="#_ftnref22" id="_ftn22"><sup>[22]</sup></a>         <i> Daz
              dir ein welch buch it gea(n)t | Da der kvnig artus | Hiez chriben vo(n)
                      orte vnze ende vz | Von ir aller mu(n)de | Der von der tofelrunde | Daz buch
                      er alle zit gerne laz | Wan ez wor vn(de) <span class="ras">bewert</span>
                      waz</i>  (Ep 198204; Sch.
                  850.1218). Siehe dazu <span style="font-variant:small-caps">Strohschneider</span>
