          <div id="ftn44">
            <p class="MsoFootnoteText"><a href="#_ftnref44" id="_ftn44"><sup>[44]</sup></a>         <i> <b>P</b>filippez
                      colin der goltmit</i> (Ep 527;
                  Sch. 857.21). <span style="font-variant:small-caps">Bumke</span> hlt es fr
                  mglich, dass es sich bei der Berufsbezeichnung Goldschmied um eine
                  Dichtermetapher handelt; vgl. <span style="font-variant:small-caps">Bumke</span>
                  (1997), S. 88, Anm. 2. Tatschlich kann die Aussage Kolins <i>Jch bin din tihter
                      gein | Vn(de) bedarf der helfe din | Nv teil mir dine milte mit | So wurde ich
                      wider ein goltmit</i> (Ep 541544; Sch. 858.912) als Bitte
                  gedeutet werden, weiterhin als ,Verseschmied ttig sein zu knnen.
                  Andererseits kann sich auch ein Goldschmied mit der Buchproduktion beschftigt
                  haben. So war bspw. Johannes Gutenberg (um 1400 bis 1468), der Erfinder der
                  auswechselbaren beweglichen Lettern, von Beruf nicht nur Schreiber, sondern
                  auch Goldschmied. In Straburg wurden die Goldschmiede 1362 der bereits
                  vorhandenen Schilter- und Malerzunft zugeteilt, die neu den Namen
                  ,Goldschmiedzunft trug; vgl. <span style="font-variant:small-caps">Meyer</span>
                  (1881), S. 159f., und <span style="font-variant:small-caps">Jschke</span>
                  (1994), S. 140143.</p>
