          <div id="ftn104">
            <p class="MsoFootnoteText"><a href="#_ftnref104" id="_ftn104"><sup>[104]</sup></a> Die Besitzeintrge entsprechen den
                Gegebenheiten der Zeit. So findet sich ein graphisch sehr hnlicher
                Besitzeintrag der Grfin Kunigunde von Knigsegg, geb. Grfin von Zimmern (
                1602), aus dem Jahr 1592 mit der Devise <i>GHMHW</i> (Gott hilf mir, halte
                Wort oder Gott hilf mir, hilf wohl) im Vorderspiegel der Handschrift
                Stuttgart, WLB, Cod.Don.III.54, von 1540/1550: <i>.1.5. B. 9.2. | .G.H.M.H.W. </i>|
                <i>Knigunde fra fraw z Kunigseckh vnd | Alendorf geborne greffin Z Zimbern
                  | gehort dis Bch Z</i>; vgl. das Digitalfaksimile des <a href="http://digital.wlb-stuttgart.de/sammlungen/sammlungsliste/werksansicht/?no_cache=1&amp;tx_dlf%5Bid%5D=2653&amp;tx_dlf%5Bpage%5D=2&amp;tx_dlf%5Bdouble%5D=0&amp;cHash=52449b2ada5fbb79ed3a18bafa2e5b8" target="_blank"><span style="">Cod.Don.III.54</span></a> der Wrttembergischen Landesbibliothek
                Stuttgart.  Mit hnlichen Bcherzeichen notierte der Sammler von
                Reformationsschriften Ulrich VI. Graf von Rappoltstein ( 1531), Sohn Wilhelms
                II., 1522 mit Anna Alexandrina Grfin von Frstenberg vermhlt, die
                Erwerbsjahre seiner Bcher; vgl. <span style="font-variant:small-caps">Bartholdi</span>
                (1861/62), S. 46; <span style="font-variant:small-caps">Kimmenauer</span>
                (1959/60) (hier als Ulrich IX. bezeichnet, Abbildung eines Zeichens auf S. 22),
                sowie <span style="font-variant:small-caps">Stammtafeln</span> XI, Tafel 82
                (Ulrich VI).
            </p>
