          <div id="ftn133">
            <p class="MsoFootnoteText"><a href="#_ftnref133" id="_ftn133"><sup>[133]</sup></a>
            <span style=" font-variant:small-caps"> Schneider</span><span style=""> (2009),
                  S. 120: In der kalligraphischen, vllig gleichmigen Textura des
                  Epenschreibers stehen, mit Ausnahme der Bogenverbindungen von de und do, die
                  Buchstaben unverbunden nebeneinander; die Schfte sind meist doppelt gebrochen,
                  oft mit Quadrangelbildung am Abschlu auf der Zeile. Konkave Einwlbungen
                  lassen die Winkel noch schrfer hervortreten. Die Oberschfte beginnen gegabelt,
                  Doppelschfte sind nicht durchgehend mit Horizontalstrich verbunden,
                  auslautendes -f und -t werden mit teilweise gebogenem Schlustrich verziert; r
                  mit breitem, oft gebrochenem Fustrich bleibt undekoriert. Entsprechend dem
                  hohen Niveau ist das kastenfrmige a wenig berhht und der g-Unterbogen reicht
                  unter die Zeile. Auf i stehen hufig Hkchen, der k-Abschlubogen reicht auf
                  die Zeile, rundes r wird auch nach o nicht verwendet. Der Schreiber kennt die
                  tt-Ligatur mit hohem, gegabeltem zweitem Schaft; das geschwnzte z schwingt
                  unter der Zeile nach rechts um. Den Majuskeln wird oft vor dem eigentlichen
                  Buchstabenkrper auf gemeinsamem Fustrich ein Bogen oder spitzer Haken
                  vorgestellt, die Hauptschfte sind hufig mit Zacken verziert. Akzente fehlen
                  ganz, Abbreviaturen werden, da kein Platzmangel dazu zwingt, kaum verwendet.
                  Zu den Abweichungen siehe <span style="font-variant:small-caps">Fasching</span>
                  (2018), S. 499f.</span></p>
